
.abnomal-content .abnormal-btns-order {
  margin-bottom: 10px;
}
.abnomal-content .abnomal-content-wrapper {
  height: calc( 100% - 38px);
}
.abnomal-content .clickimglist {
  width: 100%;
  overflow: auto;
  padding-bottom: 2px;
}
.abnomal-content .cell .clickimg {
  border: 1px solid #ccc;
  padding: 2px;
  text-align: center;
  width: 50px;
  height: 50px;
  display: inline-block;
  margin-left: 3px;
  line-height: 50px;
}
.abnomal-content .cell .clickimg img {
    max-width: 100%;
    max-height: 100%;
    vertical-align: middle;
}



