
.addEmployeerPop {
  left: auto;
  top: 50px;
  bottom: auto;
  min-width: 600px;
  max-width: 600px;
}
.addEmployeerPop .popRight-content {
    padding: 20px 20px 0;
    box-sizing: border-box;
}
.addEmployeerPop .nativeinput {
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    width: 100%;
    font-size: 12px;
}
.addEmployeerPop .el-select .el-input__inner {
    padding-right: 15px;
}
.highlight {
  font-style: normal;
  color: #f00;
}



