
@keyframes showUlAni {
0%,
  10% {
    height: 0;
}
11%,
  21% {
    height: 30px;
}
22%,
  32% {
    height: 60px;
}
33%,
  43% {
    height: 90px;
}
44%,
  54% {
    height: 120px;
}
55%,
  65% {
    height: 150px;
}
66%,
  76% {
    height: 180px;
}
77%,
  87% {
    height: 210px;
}
88%,
  99% {
    height: 240px;
}
100% {
    height: 100%;
}
}
.check_box {
  min-width: 1100px;
}
.check_box .el-header {
    margin-top: 20px;
}
.check_box .el-header .top_content {
      border: 1px solid #bcbcbc;
      height: 143px;
      background-image: url(../../assets/checkImg/bgo1.png);
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      padding: 45px 54px;
      box-sizing: border-box;
}
.check_box .el-header .top_content h6 {
        font-size: 22px;
        font-weight: normal;
        color: #ffffff;
}
.check_box .el-header .top_content .top_ts {
        margin-top: 8px;
        font-size: 16px;
        font-weight: normal;
        color: #ffffff;
}
.check_box .el-header .top_content .top_ts .top_num {
          color: #ff0000;
          margin: 0 5px;
          font-weight: bold;
}
.check_box .el-header .box_top .top_content2 {
      height: 140px;
      line-height: 24px;
      padding: 45px 126px;
      border: 1px solid #bcbcbc;
      background-image: url(../../assets/checkImg/bgo1.png);
      background-image: url(../../assets/checkImg/bgo1.png);
      background-repeat: no-repeat;
      background-size: cover;
      position: relative;
}
.check_box .el-header .box_top .top_content2 h6 {
        font-size: 30px;
        color: #fff;
        font-weight: normal;
}
.check_box .el-header .box_top .top_content2 h6 .btn_qx {
          padding: 4px 12px;
          border: 1px solid #fff;
          background: #409EFF;
          color: #fff;
          margin-left: 12px;
}
.check_box .el-header .box_top .top_content2 h6 .btn_qx:hover {
          background: #fff;
          color: #409EFF;
}
.check_box .el-header .box_top .top_content2 h6 .top_num {
          color: #ff0000;
          margin: 0 5px;
}
.check_box .el-header .box_top .top_content2 .top_ts {
        width: 144px;
        height: 20px;
        font-size: 16px;
        font-weight: normal;
        color: #fff;
        overflow: hidden;
        margin-top: 10px;
}
.check_box .el-header .box_top .top_content2 .top_ts .center_title {
          color: #fff;
}
.check_box .el-header .box_top .top_content2 .wzlicon {
        width: 40px;
        height: 45px;
        background-image: url(../../assets/checkImg/anquan.png);
        font-size: 45px;
        color: #fff;
        position: absolute;
        left: 62px;
        top: 35px;
}
.check_box .el-main {
    padding: 0 20px;
}
.check_box .el-main .btn_qx {
      padding: 3px 10px;
      margin: 14px 0;
      font-size: 14px;
      width: 58px;
      float: right;
}
.check_box .el-main .btn_qx1 {
      width: 58px;
      padding: 3px 10px;
      margin: 14px 0;
      font-size: 14px;
      float: right;
      border: 1px solid #bcbcbc;
      color: #bcbcbc;
      display: block;
}
.check_box .el-main :hover.btn_qx1 {
      border: 1px solid #3e9ff1;
      background: #fff;
      color: #3e9ff1;
}
.check_box .el-main .main_checker {
      text-align: center;
      margin-top: 50px;
}
.check_box .el-main .main_checker .el-button {
        width: 260px;
        height: 60px;
        background-color: #07a6f9;
        font-size: 30px;
        margin-top: 20%;
}
.check_box .el-main .main_checker2 {
      border: 1px solid #bcbcbc;
}
.check_box .el-main .main_checker2 h6 {
        height: 46px;
        line-height: 46px;
        font-size: 16px;
        font-weight: normal;
        border-bottom: 1px solid #bcbcbc;
}
.check_box .el-main .main_checker2 p {
        font-size: 14px;
}
.check_box .el-main .main_checker2 p .el-icon-success {
          color: #00cc00;
          margin-right: 10px;
}
.check_box .el-main .main_checker2 p .el-icon-warning {
          color: red;
          margin-right: 10px;
}
.check_box .el-main .main_checker2 .company_content {
        min-height: 320px;
        margin-top: -11px;
}
.check_box .el-main .main_checker2 .company_content ul {
          height: 0;
          margin-top: 25px;
          overflow: hidden;
          animation: showUlAni 1.2s linear forwards;
          animation-play-state: paused;
}
.check_box .el-main .main_checker2 .company_content ul.showani {
            animation-play-state: running;
}
.check_box .el-main .main_checker2 .company_content ul.cancelAni {
            animation-play-state: paused;
}
.check_box .el-main .main_checker2 .company_content ul li {
            line-height: 50px;
            border-bottom: 1px solid #ccc;
            padding: 0 60px;
}
.check_box .el-main .main_checker2 .btn_content {
        text-align: center;
        margin: 20px 0;
}
.check_box .el-main .main_checker2 .btn_content .btn_kd {
          width: 110px;
          font-size: 16px;
}
.check_box .el-main .main_checker2 .btn_content .btn_h {
          width: 110px;
          font-size: 16px;
          background-color: #ccc;
          border: 1px solid #ccc;
}



