
.addPickUpPop {
  left: auto;
  top: 50px;
  bottom: auto;
  min-width: 900px;
  max-width: 900px;
}
.addPickUpPop .popRight-content {
    /*padding: 20px 0px 0;*/
    box-sizing: border-box;
}
.addPickUpPop .el-input__inner {
    color: #3e9ff1;
}
.addPickUpPop .el-textarea__inner {
    color: #3e9ff1;
}
.addPickUpPop .el-form-item.is-success .el-input__inner, .addPickUpPop .el-form-item.is-success .el-input__inner:focus, .addPickUpPop .el-form-item.is-success .el-textarea__inner, .addPickUpPop .el-form-item.is-success .el-textarea__inner:focus {
    border-color: #dcdfe6;
}
.addPickUpPop .nativeinput {
    border-radius: 4px;
    border: 1px solid #dcdfe6;
    font-size: 12px;
}
.senderName_lrl .el-autocomplete {
  width: 178px;
}
.senderAddr_lrl .el-autocomplete {
  width: 745px;
}
.order_toCityCode .el-input--suffix .el-input__inner {
  padding-right: 15px;
}
.selectListOption_lrl {
  clear: both;
}
.selectListOption_lrl span {
    display: inline-block;
    width: 50%;
}
.selectListOption_lrl .addr {
    text-align: right;
    color: #999;
    font-size: 12px;
}
.pickUp-order, .pickUp-bottom {
  padding-top: 10px;
  border-top: 1px solid #e4e7ed;
}
.pickUp-order .order_toCityCode, .pickUp-bottom .order_toCityCode {
    margin-right: 10px !important;
}
.pickUp-order .order_remark .el-form-item__label, .pickUp-bottom .order_remark .el-form-item__label {
    /*text-align: center;*/
}
.pickUp-order .order_remark .el-form-item__content, .pickUp-bottom .order_remark .el-form-item__content {
    width: 460px;
}
.pickUp-order .el-form-item--mini .el-input__inner, .pickUp-bottom .el-form-item--mini .el-input__inner {
    /*width: 90%;*/
}
.pickUp-order .customer-unit, .pickUp-bottom .customer-unit {
    width: 80%;
}
.pickUp-order .checked, .pickUp-bottom .checked {
    display: block;
}
.pickUp-order .checked .el-form-item__content, .pickUp-bottom .checked .el-form-item__content {
      padding-left: 14px;
}
.pickUp-order .pickup-way .el-input {
  width: 91%;
}
.pickUp-order .el-input:nth-of-type(5) {
  /*.el-input__inner{*/
  width: 100%;
  /*}*/
}
.pickUp-bottom .arrive-time .el-input__inner {
  width: 80%;
}
.pickUp-bottom .el-form-item--mini:nth-of-type(5) .el-input__inner, .pickUp-bottom .el-form-item--mini:nth-of-type(5) .nativeinput {
  width: 91%;
}
.pickUp-bottom .el-form-item--mini:nth-of-type(6) .el-input--mini {
  width: 93%;
}
.pickUp-bottom .el-form-item--mini:nth-of-type(8) .el-input__inner, .pickUp-bottom .el-form-item--mini:nth-of-type(8) .nativeinput {
  width: 91%;
}
.pickUp-bottom .el-form-item--mini:nth-of-type(9) .el-input__inner, .pickUp-bottom .el-form-item--mini:nth-of-type(9) .nativeinput {
  width: 75%;
}
.pickUp-bottom .el-form-item--mini:nth-of-type(9) .el-date-editor.el-input {
  width: 105% !important;
}
.pickUp-top {
  margin-top: 10px;
}
.pickUp-top .el-input.is-disabled .el-input__inner {
  background-color: transparent;
}
.pickup-way .el-input__suffix {
  right: 20px;
}
.pickup_lrl .el-input.is-disabled .el-input__inner, .pickup_lrl .el-textarea.is-disabled .el-textarea__inner {
  background-color: #fff;
  color: #3e9ff1;
}
.pickup_lrl {
  padding-left: 10px;
}
.pickup_lrl .info_date {
    background: #e9f3fa;
    padding: 8px 5px;
    font-size: 14px;
    color: #666;
}



