.navbar .user-avatar {
  border-radius: 50%;
  vertical-align: middle;
}
.navbar .avatar-container {
  padding: 0 23px 0 30px;
  height: 50px;
  float: right;
  white-space: nowrap;
  position: relative;
}
.navbar .avatar-container::before {
    position: absolute;
    content: '';
    top: 15px;
    height: 20px;
    width: 1px;
    background: #eee;
    left: 0;
}
.navbar .avatar-container:hover {
    background: rgba(255, 255, 255, 0.1);
}
.navbar .avatar-container .avatar-wrapper {
    cursor: pointer;
    position: relative;
}
.navbar .avatar-container .avatar-wrapper .user-avatar {
      width: 36px;
      height: 36px;
      margin-right: 12px;
}
.navbar .avatar-container .avatar-wrapper .user-name {
      display: inline-block;
      max-width: 64px;
      color: #fff;
      line-height: 50px;
      vertical-align: middle;
      text-overflow: ellipsis;
      overflow: hidden;
}
.popoveruser {
  padding: 10px 0 0;
}
.popoveruser .popoveruser_info_lyy p {
    line-height: 32px;
    font-size: 16px;
}
.popoveruser iframe {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
}
.popoveruser-avatar {
  text-align: center;
}
.popoveruser-avatar .user-avatar {
    border-radius: 50%;
    width: 80px;
    height: 80px;
    border: 2px solid #42485B;
}
.popover-btns {
  margin-top: 20px;
  text-align: center;
}



