
.dashboard-container {
  padding: 30px;
}
.operation_flow {
  width: 1228px;
  height: 752px;
  margin: 53px auto 0;
  background-image: url("../../assets/flow/index2.png");
  background-repeat: no-repeat;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 20px;
}
.operation_flow .operation_flow_item {
    height: 210px;
    width: 83%;
    margin: 0 0 40px 0;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 70px 100px 60px;
    text-align: center;
    font-size: 14px;
    color: #666;
}
.operation_flow .operation_flow_foot_right,
  .operation_flow .operation_flow_foot_left {
    width: 28%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.operation_flow .operation_flow_foot_left {
    margin: 40px 0 0 40px;
}
.operation_flow .operation_flow_foot_right {
    margin: 40px 50px 0 0px;
}
.flowicon {
  transition: 0.5s;
}
.flowicon:hover {
  transform: scale(1.2);
  transition: 0.3s;
}



