/** - **/
/** - **/
.tags-view-container {
  position: absolute;
  height: 31px;
  top: 50px;
  background: #e6e6e6;
  z-index: 999;
  padding-right: 50px;
  left: 0;
  width: 100%;
}
.tags-view-container .tags-view-more {
    width: 50px;
    height: 30px;
    text-align: center;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 2;
    background: #E6E6E6;
}
.tags-view-container .tags-view-more .el-icon-arrow-down {
      cursor: pointer;
      line-height: 30px;
}
.tags-view-container .tags-view-more:hover {
      background: #eee;
}
.tags-view-container .tags-view-more:hover .contextmenu-box {
        display: block;
}
.tags-view-container .tags-view-wrapper {
    height: 31px;
    font-size: 0;
}
.tags-view-container .tags-view-wrapper .tags-view-item {
      display: inline-block;
      position: relative;
      height: 30px;
      line-height: 30px;
      border-right: 2px solid #b8b8b8;
      color: #495060;
      background: #e6e6e6;
      padding: 0 30px;
      font-size: 14px;
}
.tags-view-container .tags-view-wrapper .tags-view-item:first-of-type {
        margin-left: 0;
}
.tags-view-container .tags-view-wrapper .tags-view-item.active {
        background-color: #fff;
        color: #666;
        position: relative;
        height: 31px;
}
.tags-view-container .contextmenu-box {
    margin: 0;
    background: #fff;
    z-index: 100;
    position: absolute;
    border-radius: 4px;
    padding: 2px 0;
    box-shadow: 0px 0px 3px 0 rgba(0, 0, 0, 0.3);
    position: absolute;
    right: 2px;
    top: 31px;
    display: none;
}
.tags-view-container .contextmenu-box .el-icon-check {
      font-size: 6px;
      position: absolute;
      top: 9px;
      left: 5px;
      display: none;
}
.tags-view-container .contextmenu-box .active-menu {
      color: #2887e0;
}
.tags-view-container .contextmenu-box .active-menu .el-icon-check {
        display: block;
}
.tags-view-container .contextmenu {
    margin: 0;
    background: #fff;
    list-style-type: none;
    padding: 5px 0;
    font-size: 12px;
    white-space: nowrap;
    font-weight: 400;
    color: #333;
}
.tags-view-container .contextmenu li {
      margin: 0;
      padding: 7px 25px;
      cursor: pointer;
      position: relative;
}
.tags-view-container .contextmenu li:hover {
        background: #eee;
}
.tags-view-container .menu-line {
    padding: 0;
    height: 1px;
    background: #eee;
}
.tags-view-container .contextmenu-list {
    max-height: 350px;
    overflow: auto;
}
.tags-view-wrapper .tags-view-item.active:hover .el-icon-refresh {
  display: block;
}
.tags-view-wrapper .tags-view-item:hover .el-icon-close {
  color: #f00;
  font-weight: bold;
}
.tags-view-wrapper .tags-view-item .el-icon-refresh {
  position: absolute;
  top: 8px;
  left: 10px;
  display: none;
}
.tags-view-wrapper .tags-view-item .el-icon-refresh:hover {
    color: #2887e0;
    font-weight: bold;
}
.tags-view-wrapper .tags-view-item .el-icon-close {
  position: absolute;
  top: 10px;
  right: 10px;
  margin-left: 10px;
  border-radius: 50%;
  text-align: center;
  font-size: 12px;
}
.scroll-container {
  white-space: nowrap;
  position: relative;
  overflow-x: hidden;
  overflow-y: visible;
  width: 100%;
}
.scroll-container:after {
    content: '';
    width: 100%;
    height: 1px;
    position: absolute;
    left: 0;
    bottom: 0;
    background: #DCDCDC;
}
.scroll-container .scroll-wrapper {
    position: absolute;
    z-index: 2;
    height: 30px;
}



