
.loadChart_wrapper {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border: 1px solid #cdf;
  border-top: 2px solid #b8cbd5;
}
.loadChart_wrapper .loadChart_head {
    height: 38px;
    width: 100%;
    padding-right: 5px;
    padding-top: 3px;
    border-bottom: 1px solid #cdf;
}
.loadChart_wrapper .loadChart_head .el-button--primary.is-plain {
      float: right;
}
.loadChart_wrapper .loadChart_chart {
    width: 100%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.loadChart_wrapper .loadChart_chart .chartItem {
      -ms-flex: 1;
          flex: 1;
      position: relative;
}
.loadChart_wrapper .loadChart_chart .chartItem b {
        color: red;
        position: absolute;
        top: 10px;
        left: 10px;
}
.loadChart_wrapper .loadChart_chart .chartItem .chartItemDetail {
        display: block;
        text-align: center;
}
.loadChart_wrapper .loadChart_chart .chartItem canvas {
        height: 100%;
        width: 100%;
}



