
.remarksPopSetup .el-dialog__header, .remarksPopSetup .el-dialog__footer {
  background: #eee;
  padding: 10px;
}
.remarksPopSetup .el-dialog__headerbtn {
  top: 10px;
  right: 10px;
}
.remarksPopSetup .el-dialog__body {
  padding: 10px 0 20px;
  position: relative;
}
.remarksPopSetup .el-dialog__title {
  font-size: 14px;
}
.remarksPopSetup .el-input {
  width: 180px;
  margin-left: 5px;
}
.remarksPopSetup .el-input .el-input__inner {
    border-color: #cce1f9;
}
.remarksPopSetup .el-button + .el-button {
  margin-left: 5px;
}
.remarksPopSetup .el-button {
  margin-left: 5px;
}
.remarksPopSetup .el-button i {
    font-weight: bold;
}
.remarksPopSetup .add-btn {
  color: #3b95ee;
  border: 1px solid #3b95ee;
}
.remarksPopSetup .delete-btn {
  color: #f00;
  border: 1px solid #f00;
}
.remarksPopSetup .setup-info-remark {
  height: 258px;
  overflow: auto;
}
.remarksPopSetup .setup-info-remark li {
    border-bottom: 1px solid #ccc;
    line-height: 30px;
}
.remarksPopSetup .setup-info-remark .remark-info {
    padding-left: 10px;
    color: #666;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}



