.topNav-message {
  position: absolute;
  top: 0;
  right: 264px;
  width: 80px;
  text-align: center;
  height: 50px;
  line-height: 16px;
  color: #fff;
  cursor: pointer;
}
.message-wrapper {
  height: 50px;
  padding-top: 27px;
  padding-right: 22px;
  padding-left: 18px;
}
.message-wrapper .svg-icon {
    margin-right: 0px;
    font-size: 1.5em;
    vertical-align: middle;
}
.message-wrapper .el-badge__content {
    border-radius: 8px;
    padding: 0 4px;
}
.message-wrapper::after, .message-wrapper::before {
    position: absolute;
    content: '';
    top: 15px;
    height: 20px;
    width: 1px;
    background: #87cfff;
}
.message-wrapper::after {
    left: 0;
}
.message-wrapper::before {
    right: 0;
}
.popovermsg {
  padding: 0;
}
.msgbox-header {
  background: #eee;
  line-height: 30px;
}
.msgbox-body {
  height: 150px;
  overflow-y: auto;
  overflow-x: hidden;
}



