.popRight {
  width: 546px;
  position: fixed;
  right: 0;
  top: 50px;
  height: calc( 100% - 50px);
  z-index: 33;
  padding-top: 30px;
  padding-bottom: 76px;
  background: #fff;
  border-radius: 6px 0px 0px 6px;
  box-shadow: -2px 0px 8px 0px rgba(169, 169, 169, 0.78);
  transition: transform 0.6s ease;
  transform: translate(100%, 0);
}
.showPopRight {
  transform: translate(0, 0);
}
.popRight-header {
  height: 30px;
  line-height: 30px;
  background-color: #42485B;
  color: #fff;
  text-align: center;
  top: 0;
  left: 0;
  width: 100%;
  position: absolute;
  border-radius: 6px 0px 0px 0px;
}
.popRight-header i {
    position: absolute;
    top: 0;
    right: 9px;
    line-height: 30px;
    cursor: pointer;
}
.popRight-header i:hover {
      background: rgba(0, 0, 0, 0.1);
}
.popRight-content {
  position: relative;
  height: 100%;
  overflow: auto;
}
.popRight-footer {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 76px;
  line-height: 76px;
  text-align: center;
  width: 100%;
}



