
@charset "UTF-8";
.html5index {
  min-width: 1100px;
  height: 100%;
  min-height: 666px;
  background: #ebebeb;
}
.main_content {
  padding: 15px;
  overflow-y: hidden;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.main_content .head_title {
    height: 31px;
    line-height: 31px;
    overflow: hidden;
    border-radius: 2px;
    background: #fff;
    box-sizing: border-box;
}
.main_content .head_title h4 {
      font-size: 14px;
      display: inline-block;
}
.main_content .head_title ul {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: right;
}
.main_content .head_title ul li {
        display: inline-block;
        min-width: 45px;
        text-align: center;
        white-space: nowrap;
        font-size: 14px;
        color: #333;
        /*  */
        cursor: pointer;
        box-sizing: border-box;
}
.main_content .head_title ul li .el-range-editor.el-input__inner {
          height: 4%;
          line-height: 30px;
          border: none;
}
.main_content .head_title ul li .el-date-editor .el-range__close-icon {
          position: absolute;
          right: 10%;
          top: 0%;
}
.main_content .head_title ul li .el-date-editor .el-range__icon {
          display: none;
}
.main_content .head_title ul li .el-date-editor .el-range-separator {
          line-height: 25px;
}
.main_content .head_title ul li .el-range-editor .el-range-input {
          margin-left: 10px !important;
}
.main_content .head_title ul li:hover, .main_content .head_title ul li.active {
        color: #3e9ff1;
        transform: scale(1.4);
}
.main_content .head_title ul li:nth-child(6):hover {
        transform: none;
}
.main_content .main_forthUl {
    overflow: hidden;
    box-sizing: border-box;
    height: 215px;
}
.main_content .main_forthUl ul {
      box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
      background: #fff;
      height: 185px;
      margin: 15px 0 15px 0;
      box-sizing: border-box;
      position: relative;
      transition: color 4s ease;
      border-radius: 5px;
}
.main_content .main_forthUl ul .box_gg, .main_content .main_forthUl ul .box_gg1 {
        position: absolute;
        width: 8%;
        height: 16%;
        top: 0.1%;
        left: 92.2%;
        display: none;
        background-repeat: no-repeat;
        background-position: right;
        box-sizing: border-box;
        z-index: 10000;
}
.main_content .main_forthUl ul .box_gg img, .main_content .main_forthUl ul .box_gg1 img {
          background-size: cover;
          width: 100%;
          height: 100%;
}
.main_content .main_forthUl ul li {
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        padding: 0 20px;
        box-sizing: border-box;
        border-radius: 5px 5px 0 0;
}
.main_content .main_forthUl ul li span {
          color: red;
          padding: 0 10px;
}
.main_content .main_forthUl ul li em {
          float: right;
}
.main_content .main_forthUl ul li em i {
            margin-left: 5px;
}
.main_content .main_forthUl ul li:nth-child(1) {
        border-bottom: 1px solid #ddd;
}
.main_content .main_forthUl ul:nth-child(4) {
      margin: 15px 0 15px 0;
}
.main_content .main_forthUl ul:hover {
    border: 1px solid #3e9ff1;
    cursor: pointer;
    box-sizing: border-box;
    transition: all 0.2s ease-out;
    box-shadow: 0px 35px 77px -17px rgba(0, 0, 0, 0.4);
    overflow: hidden;
    color: #3e9ff1;
}
.main_content .main_forthUl ul:hover .box_gg {
    display: none;
}
.main_content .main_forthUl ul:active .box_gg1 {
    display: none;
}
.main_content .main_left {
    padding: 0 0 !important;
    -ms-flex: 1;
        flex: 1;
    overflow: hidden;
}
.main_content .main_left .el-col {
      height: 100%;
}
.main_content .main_left .ul_left {
      background: #fff;
      height: 100%;
      box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
      border: 1px solid #ddd;
}
.main_content .main_left .ul_right {
      height: 100%;
}
.main_content .main_left .ul_right li {
        width: 100%;
        height: 50%;
        padding-top: 7.5px;
}
.main_content .main_left .ul_right li .inner_div {
          height: 100%;
          background: #fff;
          border-radius: 5px;
          padding-top: 30px;
          box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
          border: 1px solid #ddd;
}
.main_content .main_left .ul_right li .inner_div > div {
            height: 100%;
}
.main_content .main_left .ul_right li canvas {
          height: 100%;
}
.main_content .main_left .ul_right li p {
          margin-top: -30px;
          font-size: 14px;
          padding: 7px;
          border-radius: 5px 5px 0 0;
}
.main_content .main_left .ul_right li:nth-child(1) {
        padding-bottom: 7.5px;
        padding-top: 0;
}
.main_content .main_left .ul_right .inner_div:hover {
        cursor: pointer;
        box-sizing: border-box;
        transition: all 0.2s ease-out;
        box-shadow: 0px 35px 77px -17px rgba(0, 0, 0, 0.1);
        overflow: hidden;
        color: #3e9ff1;
        transform: translateY(-3px);
        transition-delay: 0.2s;
}
.main_content .main_left .ul_right #main {
        height: 100%;
}



