
.sidebar-menu-search {
  position: absolute;
  top: 40px;
  left: 0;
  width: 100%;
  height: 30px;
  padding-left: 3px;
}
.sidebar-menu-search .el-input {
    width: 120px;
    font-size: 12px;
}
.sidebar-menu-search .el-input input {
      height: 24px;
      line-height: 24px;
      border-radius: 4px;
      border: solid 1px rgba(255, 255, 255, 0.6);
      background: transparent;
      color: #fff;
}
.sidebar-menu-search .el-input .el-input__icon {
      line-height: 24px;
}
.sidebar-menu-pop {
  left: 3px !important;
}
.sidebar-menu-pop .el-autocomplete-suggestion li, .sidebar-menu-pop .el-autocomplete-suggestion__wrap {
    padding: 0;
}



