
.tableHeadItemBtn2 {
  height: 100%;
  position: relative;
}
.tableHeadItemBtn2 .el-button {
    border: none;
}
.tableHeadItemBtn2 .el-button--mini,
  .tableHeadItemBtn2 .el-button--mini.is-round {
    padding: 5px 9px;
}
.tableHeadItemBtn2 .tableItemBtnMinus,
  .tableHeadItemBtn2 .tableItemBtn {
    width: 18px;
    height: 18px;
    background-size: 18px;
    background-repeat: no-repeat;
}
.tableHeadItemBtn2 .tableAllBtnMinus,
  .tableHeadItemBtn2 .tableAllBtn {
    width: 18px;
    height: 18px;
    position: absolute;
    z-index: 33;
    top: 10px;
    left: 67px;
    background-size: 18px;
    background-repeat: no-repeat;
}
.tableHeadItemBtn2 .tableAllBtn,
  .tableHeadItemBtn2 .tableItemBtn {
    background-image: url("../../../../../assets/png/01zengjia-c.png");
}
.tableHeadItemBtn2 .tableAllBtn:hover,
  .tableHeadItemBtn2 .tableItemBtn:hover {
    background-image: url("../../../../../assets/png/02zengjia.png");
}
.tableHeadItemBtn2 .tableAllBtnMinus,
  .tableHeadItemBtn2 .tableItemBtnMinus {
    background-image: url("../../../../../assets/png/03shanqu-c.png");
}
.tableHeadItemBtn2 .tableAllBtnMinus:hover,
  .tableHeadItemBtn2 .tableItemBtnMinus:hover {
    background-image: url("../../../../../assets/png/04shanqu.png");
}
.tableHeadItemBtn2 .showAllTable {
    width: calc(100% - 100px);
}



