
.addIOManagePop {
  left: auto;
  top: 50px;
  bottom: auto;
  min-width: 600px;
  max-width: 600px;
  z-index: 999 !important;
}
.addIOManagePop .popRight-content {
    padding: 20px 0px 0;
    box-sizing: border-box;
}
.addIOManagePop .el-form-item .el-form-item__content {
    width: 67%;
}
.addIOManagePop .el-form-item.iom_textarea {
    width: 98%;
}
.addIOManagePop .el-form-item.iom_textarea .el-form-item__content {
      width: 83%;
}
.senderName_lrl .el-autocomplete {
  width: 430px;
}
.selectListOption_lrl {
  clear: both;
}
.selectListOption_lrl span {
    display: inline-block;
    width: 50%;
}
.selectListOption_lrl .addr {
    text-align: right;
    color: #999;
    font-size: 12px;
}
.iommanage-bottom .arrive-time .el-form-item__label {
  width: 108px !important;
}
.pickup-way .el-input__suffix {
  right: 20px;
}
.pickup_lrl .el-input.is-disabled .el-input__inner, .pickup_lrl .el-textarea.is-disabled .el-textarea__inner {
  background-color: #fff;
  color: #666;
}
.pickup_lrl {
  padding-left: 10px;
}



