
.transferTable_wrapper {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  border: 1px solid #cdf;
}
.transferTable_wrapper .sortable-ghost {
    opacity: .8;
    color: #fff !important;
    background: #42b983 !important;
}
.transferTable_wrapper .transferTable_main {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: calc(100% - 35px);
}
.transferTable_wrapper .transferTable_main .transferTable_main_left {
      height: 100%;
      width: 30%;
      padding-right: 10px;
      transition: 0.9s;
}
.transferTable_wrapper .transferTable_main .transferTable_main_right {
      width: 70%;
      height: 100%;
      transition: 0.9s;
}
.transferTable_wrapper .transferTable_main .transferTable_main_left_head {
      border-right: 1px solid #cdf;
      border-top: 2px solid #b8cbd5;
      position: relative;
}
.transferTable_wrapper .transferTable_main .transferTable_main_left_head .tableHeadItemForm {
        margin-left: 0px;
        position: absolute;
        top: -4px;
        left: 75px;
}
.transferTable_wrapper .transferTable_main .transferTable_main_left_head .tableHeadItemForm .el-form-item {
          margin-bottom: 0px;
}
.transferTable_wrapper .transferTable_main .transferTable_main_right_head {
      border-left: 1px solid #cdf;
      border-top: 2px solid #b8cbd5;
}
.transferTable_wrapper .transferTable_main .transferTable_main_left_head,
    .transferTable_wrapper .transferTable_main .transferTable_main_right_head {
      height: 36px;
      padding-top: 4px;
      padding-right: 4px;
      display: block;
      width: 100%;
}
.transferTable_wrapper .transferTable_main .transferTable_main_left_head b,
      .transferTable_wrapper .transferTable_main .transferTable_main_right_head b {
        line-height: 30px;
        margin-left: 10px;
        color: red;
}
.transferTable_wrapper .transferTable_main .transferTable_main_left_head b span,
        .transferTable_wrapper .transferTable_main .transferTable_main_right_head b span {
          color: #999;
          font-size: 14px;
          font-weight: 400;
}
.transferTable_wrapper .transferTable_main .transferTable_main_left_head .el-button--primary.is-plain,
      .transferTable_wrapper .transferTable_main .transferTable_main_right_head .el-button--primary.is-plain {
        float: right;
}
.transferTable_wrapper .tableHeadItemBtn {
    height: 100%;
    position: relative;
}
.transferTable_wrapper .tableHeadItemBtn .el-button {
      border: none;
}
.transferTable_wrapper .tableHeadItemBtn .el-button--mini,
    .transferTable_wrapper .tableHeadItemBtn .el-button--mini.is-round {
      padding: 5px 9px;
}
.transferTable_wrapper .tableHeadItemBtn .tableItemBtnMinus,
    .transferTable_wrapper .tableHeadItemBtn .tableItemBtn {
      width: 18px;
      height: 18px;
      background-size: 18px;
      background-repeat: no-repeat;
}
.transferTable_wrapper .tableHeadItemBtn .tableAllBtnMinus,
    .transferTable_wrapper .tableHeadItemBtn .tableAllBtn {
      width: 18px;
      height: 18px;
      position: absolute;
      z-index: 33;
      top: 10px;
      left: 67px;
      background-size: 18px;
      background-repeat: no-repeat;
}
.transferTable_wrapper .tableHeadItemBtn .tableAllBtn,
    .transferTable_wrapper .tableHeadItemBtn .tableItemBtn {
      background-image: url("../../../../../assets/png/01zengjia-c.png");
}
.transferTable_wrapper .tableHeadItemBtn .tableAllBtn:hover,
    .transferTable_wrapper .tableHeadItemBtn .tableItemBtn:hover {
      background-image: url("../../../../../assets/png/02zengjia.png");
}
.transferTable_wrapper .tableHeadItemBtn .tableAllBtnMinus,
    .transferTable_wrapper .tableHeadItemBtn .tableItemBtnMinus {
      background-image: url("../../../../../assets/png/03shanqu-c.png");
}
.transferTable_wrapper .tableHeadItemBtn .tableAllBtnMinus:hover,
    .transferTable_wrapper .tableHeadItemBtn .tableItemBtnMinus:hover {
      background-image: url("../../../../../assets/png/04shanqu.png");
}
.transferTable_wrapper .tableHeadItemBtn .showAllTable {
      width: 100%;
}



