
.showComponents {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.showComponents p {
    margin-top: 20px;
}
.showComponents h2, .showComponents h3 {
    margin-bottom: 20px;
    margin-top: 20px;
}
.showComponents .important {
    border-left: 5px solid #f00;
    text-indent: 20px;
    background: #ffe;
    margin: 10px 0;
}
.showComponents .component {
    margin-top: 20px;
    border-top: 1px dashed #000;
    padding: 20px 0;
}
.showComponents .component li {
      margin-top: 20px;
}
.showComponents .component li .type {
        color: orchid;
}
.showComponents .active {
    color: #f00;
    background: #ffe;
}
.showComponents .sidebar-demo {
    float: left;
    width: 200px;
    overflow: auto;
    height: 100%;
    border-right: 1px solid #000;
    margin-right: 30px;
}
.showComponents .sidebar-demo ol {
      list-style: decimal;
}
.showComponents .sidebar-demo li {
      line-height: 1.5;
      text-indent: 30px;
      border-bottom: 1px dashed #eee;
}
.showComponents .sidebar-demo li:hover {
        background: #ffe;
}
.showComponents .demo-contents {
    height: 100%;
    overflow: auto;
    padding-right: 30px;
}
.showComponents blockquote {
    border-left: 1px solid #000;
    padding: 10px;
    background: #eee;
    margin: 20px 0;
}



