
@charset "UTF-8";
.dep-maintain .addpopDepMain {
  top: 29%;
  bottom: auto;
  min-width: 486px;
  max-width: 486px;
}
.dep-maintain .addRelationPop-content {
  padding: 20px 20px 0;
  box-sizing: border-box;
}
.dep-maintain .el-select .el-input__inner {
  padding-right: 15px;
}
.dep-maintain .depmain-div {
  margin: 10px 0 0 10px;
}

/**/
/*depmain-add*/
.add-fixed {
  position: fixed;
  background: white;
  top: 30px;
}
.add-fixed input {
    border-color: #e4e7ed;
    width: 350px;
    margin: 5px 20px 5px 10px;
    color: #999;
    padding: 8px 0 2px 10px;
    font-size: 14px;
}
.add-fixed .el-input {
    width: 340px;
    float: left;
}
.add-fixed .addSvg {
    display: inline-block;
}
.add-fixed .addSvg span.svgSpan {
      /*cursor: pointer;*/
}
.add-fixed .addSvg span.svgSpan .lll-dot-true {
        font-size: 20px;
        margin: 10px 6px 1px 60px;
}
.add-fixed .addSvg span.svgSpan .lll-dot-false {
        font-size: 20px;
        margin-right: 15px;
}

/*depmain-edit */
.depmain-edit .addSvg {
  display: inline-block;
}
.depmain-edit .addSvg span.svgSpan1 .lll-dot-true {
    font-size: 20px;
    margin: 4px 10px 2px 50px;
    cursor: pointer;
}
.depmain-edit .addSvg span.svgSpan2 .lll-dot-false {
    font-size: 20px;
    cursor: pointer;
    margin: 3px 9px 2px 0;
}
.depmain-edit .addSvg span.svgSpan2 .lll-dot-false .svg-icon {
      fill: #bec4d1;
      font-size: 20px;
}
.depmain-edit .addSvg span.svgSpan2 .lll-dot-false:hover {
    /*display: block;*/
}
.depmain-edit .addSvg span.svgSpan2 .lll-dot-false:hover .svg-icon {
      fill: #bec4d1;
      cursor: pointer;
}
.depmain-add .el-input .el-input__inner {
  height: 30px;
}
.depmain-add .depmain-list {
  overflow: hidden;
}
.depmain-add .depmain-list ul li:nth-of-type(1) {
    margin-top: 30px;
}

/*edit*/
.depmain-edit .el-input {
  width: 340px;
  float: left;
}
.depmain-edit .el-input .el-input__inner {
  height: 30px;
  padding-left: 8px;
}

