
.wzl_addSignPop {
  left: auto;
  top: 50px;
  bottom: auto;
  min-width: 1200px;
  max-width: 1325px;
}
.wzl_addSignPop .content_top {
    padding: 33px 0 0 0;
}
.wzl_addSignPop .content_bot {
    border-top: 1px dashed #3e9ff1;
    border-bottom: 1px dashed #3e9ff1;
    margin: 0 35px;
    padding-top: 20px;
}
.wzl_addSignPop .el-date-editor.el-input, .wzl_addSignPop .el-date-editor.el-input__inner {
    width: 178px;
}
.wzl_addSignPop .el-input--suffix .el-input__inner {
    padding-right: 15px;
}
.wzl_addSignPop .bz {
    height: 35px;
    line-height: 35px;
    width: 1055px;
    padding-left: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #3e9ff1;
    background: #f4f6f9;
}
.wzl_addSignPop .bz1 {
    height: 35px;
    line-height: 35px;
    width: 1055px;
    padding-left: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #3e9ff1;
}
.wzl_addSignPop .mark .pz {
    width: 100px;
    height: 167px;
    float: left;
    text-align: center;
}
.wzl_addSignPop .mark .pz div {
      font-size: 14px;
      margin-top: 30px;
}
.wzl_addSignPop .mark .pz div .ts {
        display: block;
        color: #999999;
        font-size: 13px;
        margin-top: 3px;
}
.wzl_addSignPop .imgshow .el-upload-list--picture .el-upload-list__item {
    overflow: hidden;
    z-index: 0;
    background-color: #fff;
    border: 1px solid #3e9ff1;
    border-radius: 6px;
    box-sizing: border-box;
    margin-top: 10px;
    padding: 10px 10px 10px 90px;
    height: 92px;
    width: 100px;
    float: left;
    margin: 5px 10px;
}
.wzl_addSignPop .el-upload-list--picture-card .el-upload-list__item {
    width: 234px;
    height: 136px;
}
.wzl_addSignPop .box_container .el-upload .el-upload-dragger {
    height: 134px;
}
.wzl_addSignPop .el-upload-dragger {
    width: 232px;
    height: 134px;
}
.wzl_addSignPop .el-upload--picture-card {
    width: 234px;
    height: 136px;
}
.wzl_addSignPop .selectListOption_lrl {
    clear: both;
}
.wzl_addSignPop .selectListOption_lrl span {
      display: inline-block;
      width: 50%;
}
.wzl_addSignPop .selectListOption_lrl .addr {
      text-align: right;
      color: #999;
      font-size: 12px;
}
.wzl_addSignPop .el-input.is-disabled .el-input__inner {
    color: #3e9ff1;
}
.wzl_addSignPop .el-input--mini .el-input__inner {
    color: #3e9ff1;
}
.wzl_addSignPop .el-form-item--mini.el-form-item, .wzl_addSignPop .el-form-item--small.el-form-item {
    margin-bottom: 25px;
}



