.createOrder {
  float: right;
  height: 50px;
  padding: 0 30px;
  text-align: center;
  line-height: 50px;
  font-size: 16px;
  color: #fff;
  cursor: pointer;
}
.createOrder:hover {
    background-color: rgba(0, 0, 0, 0.1);
}
.createOrder .svg-icon {
    margin-right: 0px;
    font-size: 1.5em;
    vertical-align: middle;
    fill: #fff;
}



