
.order_img {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  background: url(../../../../../assets/load/ani/bgr.png) repeat-x 0 0 #013dbc;
}
.order_img input {
    display: block;
}
.order_img .skipBtn {
    position: absolute;
    right: 20px;
    top: 20px;
    border: 1px solid #fff;
}
.order_img .showImg {
    width: 917px;
    height: 366px;
    position: absolute;
    top: 50%;
    margin-top: -184px;
    left: 50%;
    margin-left: -458px;
    animation: movein 2s linear;
}
.order_img .showImg img {
      display: block;
      width: 100%;
      height: 100%;
}
.order_img .showImg span {
      position: absolute;
}
.order_img .stopAni .car {
    animation: shakeit 1s ease;
}
.order_img .stopAni .message li {
    animation-play-state: running !important;
}
.order_img .stopAni .lunzi1,
  .order_img .stopAni .lunzi2,
  .order_img .stopAni .lunzi3 {
    animation-play-state: paused;
}
.order_img .stopAni li,
  .order_img .stopAni .kuai1,
  .order_img .stopAni .kuai2,
  .order_img .stopAni .kuai3,
  .order_img .stopAni .kuai4,
  .order_img .stopAni .kuai5,
  .order_img .stopAni .kuai6,
  .order_img .stopAni .kuai7,
  .order_img .stopAni .kuai8,
  .order_img .stopAni .kuai9,
  .order_img .stopAni .kuai10 {
    animation-play-state: running;
}
.order_img .lunzi {
    width: 98px;
    height: 98px;
}
.order_img .lunzi1 {
    width: 97px;
    height: 97px;
    background: url(../../../../../assets/load/ani/lun1.png) no-repeat 0 0;
    left: 88px;
    top: 270px;
    animation: roll 1s linear reverse infinite;
}
.order_img .lunzi2 {
    background: url(../../../../../assets/load/ani/lun2.png) no-repeat 0 0;
    top: 270px;
    left: 561px;
    animation: roll 1s linear reverse infinite;
}
.order_img .lunzi3 {
    background: url(../../../../../assets/load/ani/lun3.png) no-repeat 0 0;
    top: 270px;
    left: 666px;
    animation: roll 1s linear reverse infinite;
}
.order_img .kuai1 {
    background: url(../../../../../assets/load/ani/1.png) no-repeat 0 0;
    left: 250px;
    top: 165px;
    width: 134px;
    height: 63px;
    animation: kuai1 1s 1s ease paused backwards;
}
.order_img .kuai2 {
    background: url(../../../../../assets/load/ani/2.png) no-repeat 0 0;
    left: 250px;
    top: 25px;
    width: 134px;
    height: 134px;
    animation: kuai1 .8s 2s ease paused backwards;
}
.order_img .kuai3,
  .order_img .kuai9 {
    background: url(../../../../../assets/load/ani/3.png) no-repeat 0 0;
    left: 392px;
    top: 96px;
    width: 135px;
    height: 132px;
    animation: kuai1 .6s 2.8s ease paused backwards;
}
.order_img .kuai4 {
    background: url(../../../../../assets/load/ani/4.png) no-repeat 0 0;
    left: 321px;
    top: 25px;
    width: 278px;
    height: 63px;
    animation: kuai1 .6s 3.2s ease paused backwards;
}
.order_img .kuai5 {
    background: url(../../../../../assets/load/ani/5.png) no-repeat 0 0;
    left: 535px;
    top: 96px;
    width: 135px;
    height: 132px;
    animation: kuai1 .6s 3.8s ease paused backwards;
}
.order_img .kuai6,
  .order_img .kuai7 {
    background: url(../../../../../assets/load/ani/6.png) no-repeat 0 0;
    left: 607px;
    top: 96px;
    width: 63px;
    height: 63px;
    animation: kuai2 .4s 4.2s ease paused backwards;
}
.order_img .kuai7 {
    left: 679px;
    top: 165px;
    animation: kuai2 .4s 4.3s ease paused backwards;
}
.order_img .kuai8 {
    background: url(../../../../../assets/load/ani/8.png) no-repeat 0 0;
    left: 607px;
    top: 25px;
    width: 135px;
    height: 134px;
    animation: kuai2 .4s 4.6s ease paused backwards;
}
.order_img .kuai9 {
    top: 96px;
    left: 750px;
    animation: kuai2 1s 5s ease paused backwards;
}
.order_img .kuai10 {
    background: url(../../../../../assets/load/ani/10.png) no-repeat 0 0;
    left: 750px;
    top: 25px;
    width: 134px;
    height: 63px;
    animation: kuai2 1s 5.4s ease paused backwards;
}
.order_img .yinying {
    background: url(../../../../../assets/load/ani/shadow.png) no-repeat 0 0;
    width: 925px;
    height: 93px;
    left: -4px;
    bottom: -56px;
}
.order_img .message {
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -100px;
    margin-top: 20px;
    width: 300px;
}
.order_img .message li {
      line-height: 36px;
      color: #fff;
      font-size: 18px;
}
.order_img .message li:nth-child(1) {
      animation: messageMove 1s 0.5s ease backwards paused;
}
.order_img .message li:nth-child(2) {
      animation: messageMove 1s 1s ease backwards paused;
}
.order_img .message li:nth-child(3) {
      animation: messageMove 1s 3s ease backwards paused;
}
.order_img .message li:nth-child(4) {
      animation: messageMove 1s 4s ease backwards paused;
}
.order_img .message li:nth-child(5) {
      animation: messageMove 1s 5s ease backwards paused;
}
@keyframes messageMove {
0% {
    transform: translate(100%, 0);
    opacity: 0;
}
100% {
    transform: translate(0, 0);
    opacity: 1;
}
}
@keyframes roll {
0% {
    transform: rotate(0deg);
}
100% {
    transform: rotate(360deg);
}
}
@keyframes movein {
0% {
    transform: translate(100%, 0);
}
100% {
    transform: translate(0, 0);
}
}
@keyframes shakeit {
0% {
    transform: rotate(0deg);
}
30% {
    transform: rotate(-2deg);
}
60% {
    transform: rotate(1deg);
}
80% {
    transform: rotate(-1deg);
}
100% {
    transform: rotate(0deg);
}
}
@keyframes kuai1 {
0% {
    transform: translate(400px, 200px) rotate(720deg);
    opacity: 0;
}
100% {
    transform: translate(0, 0) rotate(0deg);
    opacity: 1;
}
}
@keyframes kuai2 {
0% {
    transform: translate(-200px, 200px) rotate(720deg);
    opacity: 0;
}
100% {
    transform: translate(0, 0) rotate(0deg);
    opacity: 1;
}
}



