
.singleimage2 {
  margin-top: 10vh !important;
  width: 40% !important;
}
.box_container {
  width: 100%;
  position: relative;
}
.box_container:after {
    content: "";
    display: table;
    clear: both;
}
.box_container .el-upload .el-upload-dragger {
    height: 132px;
}
.box_container .image-uploader {
    width: 100%;
    height: 100%;
}
.box_container .image-preview {
    width: 100%;
    height: 100%;
    position: absolute;
    background-color: #fff;
    top: 0;
    left: 0;
}
.box_container .image-preview .image-preview-wrapper {
      position: relative;
      width: 100%;
      height: 100%;
}
.box_container .image-preview .image-preview-wrapper img {
        width: 100%;
        height: 100%;
}
.box_container .image-preview .image-preview-action {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      cursor: default;
      text-align: center;
      color: #fff;
      opacity: 0;
      font-size: 20px;
      background-color: rgba(0, 0, 0, 0.5);
      transition: opacity .3s;
      cursor: pointer;
      text-align: center;
      line-height: 200px;
}
.box_container .image-preview .image-preview-action .el-icon-delete {
        font-size: 36px;
}
.box_container .image-preview:hover .image-preview-action {
      opacity: 1;
}
.box_container .el-button {
    margin-top: 24px;
}
.box_container .el-upload__text {
    margin-top: 10px;
    margin-bottom: 5px;
    font-size: 12px;
    line-height: 30px;
}
.box_container .upload__tip {
    font-size: 12px;
    line-height: 30px;
    color: #999;
}
.box_container .upload__title {
    font-size: 13px;
    color: #666;
    margin-top: 14px;
}
.box_container .el-upload--picture-card {
    line-height: 43px;
}



