
@charset "UTF-8";
.icon_blank {
  background-size: 24px;
  display: inline-block;
  width: 24px;
  height: 24px;
  vertical-align: middle;
}
.trackInfoPop {
  width: 1000px !important;
}
.trackInfoPop .el-tabs {
    height: 100%;
}
.trackInfoPop .el-tabs .el-tabs__content {
      height: calc(100%);
}
.trackInfoPop .el-tabs .el-tabs__content .el-tab-pane {
        height: calc(100% - 90px);
}
.editInfoPop_content {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.info_box_transfer {
  padding: 10px 10px 0 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  /* ele */
  /*  */
}
.info_box_transfer .el-form--inline .el-form-item {
    margin-bottom: 0;
}
.info_box_transfer .el-step.is-vertical .el-step__head {
    width: 18px;
    position: relative;
    top: 0px;
}
.info_box_transfer .el-step.is-vertical .el-step__line {
    left: 8px;
}
.info_box_transfer .el-step__icon.is-text {
    border-color: transparent;
    border: 0;
    width: 18px;
    height: 18px;
}
.info_box_transfer .el-steps--vertical {
    height: auto;
}
.info_box_transfer .el-step__main {
    padding-bottom: 10px;
}
.info_box_transfer .el-step__description {
    padding-right: 0;
    font-size: 14px;
    color: #333;
}
.info_box_transfer .textcenter {
    text-align: center;
}
.info_box_transfer .typebox {
    background: url("../../../../../assets/png/track-bg.png") no-repeat;
    width: 118px;
    height: 24px;
    padding-left: 16px;
    text-align: center;
    color: #333;
    display: inline-block;
    line-height: 24px;
}
.info_box_transfer .location {
    background: url("../../../../../assets/png/unactive-icon.png") no-repeat;
    width: 18px;
    height: 18px;
    display: inline-block;
}
.info_box_transfer .modifybtn,
  .info_box_transfer .deletebtn {
    display: inline-block;
    display: none;
    vertical-align: middle;
    width: 20px;
    height: 20px;
    cursor: pointer;
    margin-right: 3px;
    background: url("../../../../../assets/png/delete-icon.png") no-repeat;
}
.info_box_transfer .trackactive .modifybtn, .info_box_transfer .trackactive .deletebtn {
    display: inline-block;
}
.info_box_transfer .trackactive .typebox, .info_box_transfer .firstactive .typebox {
    background: url("../../../../../assets/png/track-active.png") no-repeat;
    color: #fff;
}
.info_box_transfer .trackactive .location, .info_box_transfer .firstactive .location {
    background: url("../../../../../assets/png/active-point.png") no-repeat;
}
.info_box_transfer .trackactive .el-step__line, .info_box_transfer .firstactive .el-step__line {
    background-color: #3e9ff1;
}
.info_box_transfer .modifybtn {
    margin-left: 10px;
    background: url("../../../../../assets/png/edit-icon.png") no-repeat;
    margin-right: 8px;
}
.info_box_transfer .steptitle {
    font-size: 14px;
    color: #333;
    height: 34px;
}
.info_box_transfer .stepItem_title {
    color: #333;
    margin-top: 10px;
    padding-left: 28px;
    height: 34px;
}
.info_box_transfer .stepItem_title .el-col {
      text-align: left;
}
.info_box_transfer .stepItem_title .tracktype {
      text-align: left;
      text-indent: 20px;
}
.info_box_transfer .stepItem {
    /* 0 */
}
.info_box_transfer .stepItem .el-col > p {
      min-width: 1px;
      min-height: 1px;
}
.info_box_transfer .stepinfo {
    -ms-flex: 1;
        flex: 1;
    overflow: auto;
}
.info_box_transfer .stepinfo-footer {
    border-top: 1px solid #d8d8d8;
    height: 70px;
    padding-top: 30px;
}
.stepFrom {
  background-color: #f2f2f2;
  box-shadow: -1px -1px 10px #bbb;
  text-align: left;
  padding: 0 10px;
}
.stepFrom .el-form-item__content {
    vertical-align: middle;
}
.animated {
  animation-duration: 0.5s;
  animation-fill-mode: both;
}
@keyframes fadeInRight {
from {
    opacity: 0;
    transform: translate3d(100%, 0, 0);
}
to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
}
}
.fadeInRight {
  animation-name: fadeInRight;
}



