
@charset "UTF-8";
.tab-content.chartSender {
  width: 100%;
  height: calc(100%);
}
.tab-content.chartSender h2 {
    text-align: center;
    margin: 0px 0px 20px 0;
    color: #409EFF;
}
.tab-content.chartSender .chartIframe {
    width: 100%;
    height: 100%;
    box-shadow: 1px 1px 10px #bbb;
    overflow: hidden;
}
.tab-content.chartSender .tab_info {
    transform: translate(0, 0);
}
.hidetrigger {
  position: absolute;
  width: calc(100% - 20px);
  height: calc(100% - 100px);
}
.info_tab_report {
  height: 100%;
  padding-bottom: 30px;
  overflow: auto;
  border: 1px solid #d0d7e5;
  box-shadow: 1px 1px 20px #ddd;
  position: relative;
  /**/
}
.info_tab_report #report_settleRecordTotal_table {
    width: 100%;
    min-width: 1200px;
}
.info_tab_report #report_settleRecordTotal_table tbody tr {
      background-color: #FFF;
      transition: 0.5s;
}
.info_tab_report #report_settleRecordTotal_table tbody tr:hover {
      background-color: #ccc;
      transition: 0.3s;
}
.info_tab_report #report_settleRecordTotal_table tbody tr td:hover {
      background-color: #cdcdcd;
      transition: 0.3s;
}
.info_tab_report #report_settleRecordTotal_table tbody {
      color: #222;
      line-height: 23px;
      font-size: 13px;
}
.info_tab_report #report_settleRecordTotal_table tbody td {
        font-size: 13px;
}
.info_tab_report #report_settleRecordTotal_table tfoot {
      display: none;
}
.footTotalFee {
  width: 100%;
  position: absolute;
  min-width: 1200px;
  bottom: 0px;
  left: 0;
  z-index: 2;
}
.footTotalFee tfoot {
    color: #222;
    line-height: 24px;
    font-size: 13px;
}
.footTotalFee tfoot td {
      font-size: 13px;
      border: 1px solid #bbb;
}



