
.smsedit_wrapper {
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  overflow: hidden;
}
.smsedit_wrapper .smsedit_tite {
    line-height: 28px;
    padding: 0 0 10px 0;
}
.smsedit_wrapper .smsedit_tite b {
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      color: #909399;
}
.smsedit_wrapper .smsedit_search .el-autocomplete {
    width: 100%;
}
.smsedit_wrapper .smsedit_search .el-input-group__prepend {
    padding: 0 10px;
}
.smsedit_wrapper .smsedit_list {
    margin: 10px 0;
    overflow: auto;
    height: calc(100% - 250px);
    padding: 10px;
    border: 1px solid #dcdfe6;
}
.smsedit_wrapper .smsedit_list p {
      text-align: center;
      margin-top: 10px;
      color: #999;
      font-size: 14px;
}
.smsedit_wrapper .smsedit_list ul {
      margin: 0;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: row;
          flex-direction: row;
      -ms-flex-wrap: wrap;
          flex-wrap: wrap;
}
.smsedit_wrapper .smsedit_list ul li {
        width: 20%;
        padding: 5px 0;
        cursor: pointer;
}
.smsedit_wrapper .smsedit_list ul li span {
          transition: 0.5s;
}
.smsedit_wrapper .smsedit_list ul li span:hover {
          transition: 0.1s;
          transform: scale(1.1);
}
.smsedit_wrapper .smsedit_list ul li .el-tag--info,
        .smsedit_wrapper .smsedit_list ul li .el-tag--info .el-tag__close {
          color: #333;
}
.smsedit_wrapper .smsedit_content {
    border: 1px solid #dcdfe6;
    height: 250px;
    overflow: auto;
    position: relative;
}
.smsedit_wrapper .smsedit_content .smsedit_content_title {
      position: absolute;
      top: 10px;
      left: 10px;
      font-size: 14px;
      color: #333;
}
.smsedit_wrapper .smsedit_content .el-textarea__inner {
      height: 100%;
      padding-top: 32px;
}
.smsedit_wrapper .sortable-ghost {
    border: 1px dashed #f00;
    background: #ffe;
}



