.login-top-wrapper{
  max-width: 1230px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  /*padding: 0 20px;*/
  position: relative;
}
.login-top-wrapper a{
  position: absolute;
  right: 0;
  top: 15px;
  line-height: 25px;
  text-decoration: none;
  color: #0a84ff;
  font-weight: bold;
}
.login-top-wrapper a:hover{
  border-bottom: 1px solid #0a84ff;
}
.login-wrapper{
  max-width: 1230px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  /*padding: 0 20px;*/
  position: relative;
  height: 100%;
  min-height: 800px;
}
.container-left{
  float: left;
  margin: 280px 0 200px 0;
}

.container-left img{
  display: inline-block;
  width: 500px;
  height: 400px;
}

.container-right{
  float: right;
  margin: 219px 0 0px 0px;
  width: 335px;
}

.login-container {
  position: relative;
  height: 100%;
  min-height: 86px;
  background-image: url('../assets/login_images/bg.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: auto;
}

.container-right input:-webkit-autofill {
  box-shadow: 0 0 0px 1000px #293444 inset !important;
  -webkit-text-fill-color: #fff !important;
}

.container-right input {
  background: transparent;
  border: 0px;
  -webkit-appearance: none;
  border-radius: 0px;
  padding: 12px 5px 12px 15px;
  color: rgba(100, 100, 100, 1);
  height: 40px;
}


.svg-container {
  padding: 6px 5px 6px 15px;
  color: #889aa4;
  vertical-align: middle;
  width: 30px;
  display: inline-block;
  /*&_login {*/
  /*font-size: 20px;*/
  /*}*/
}

.login-form {
  width:100%;
  display: inline-block;
  padding: 0px 25px 23px 25px;
  background: #fff;
  border: 1px solid #13407c;
  border-radius: 5px;
  border-radius: 10px;
  transition: 0.5s;
  /*box-shadow: 1px 1px 15px rgba(0,0,0,0.4);
  -moz-box-shadow: 1px 1px 15px rgba(0,0,0,0.4);*/
  /*-moz-box-shadow: 12px 12px 12px 5px rgba(130,170,234,0.7);
  box-shadow: 12px 12px 12px 5px rgba(130,170,234,0.7);*/

}
.login-form:hover{
  transition: 0.2s;
  box-shadow: 0px 1px 30px rgba(0,0,0,0.3);
  -moz-box-shadow: 0px 1px 30px rgba(0,0,0,0.3);
}

.login-form .el-form-item {
  border-radius: 2px;
  border: 1px solid #ccc;
  background: #fff;
  /*border-radius: 5px;*/
  color: #454545;
  margin-bottom: 20px !important;
}
.login-form .el-input-group__append{
  padding: 0;
}
.login-form .el-input-group__prepend{
  /*border-radius: 5px 0 0 5px;*/
  border: 0;
  border-right: 1px solid #dcdfe6;
 
}

.login-container .el-form-item:hover{
  border-color: #409eff;
}
.previewPwd{
  padding-right:26px;
  position: relative;
}
.icon_by{
  /* background: url('../assets/login_images/by.png') no-repeat; */
  background: url('../assets/login_images/moren.jpg') no-repeat;
  width: 22px;
  height: 22px;
  position: absolute;
  left: 251px;
  top: 7px;
  cursor: pointer;
}
.icon_zy{
  /* background: url('../assets/login_images/zy.png') no-repeat; */
  background: url('../assets/login_images/kejian.jpg') no-repeat;
  width: 22px;
  height: 22px;
  position: absolute;
  left: 251px;
  top: 7px;
  cursor: pointer;
}
.container-top{
  background-color: #FFF;
  position: fixed;
  top: 0;
  z-index: 2;
  width: 100%;
  /*height: 60px;*/
}
.logo{
  text-align: center;
}

.logo-img{
  /*display: inline-block;*/
  /*width: 370px;*/
  height: 35px;
  margin:10px auto;
  vertical-align: middle;
}

.title {
  font-size: 16px;
  color: #333;
  margin: 36px auto 30px auto;
  /*text-align: center;*/
  /*font-weight: bold;*/
}

.login .el-but{
  line-height: 40px !important;
  font-size: 14px;
  background-color: #0a84ff;
  letter-spacing: 2px;
  padding: 0px 20px;
  border: none;
  color:#FFFFFF;
}

.login{
  margin-top: 42px;
  margin-bottom: 20px;
  height: 40px;
}

