.down{
  position: absolute;
  bottom: 0px;
  right: 0px;
}

.down img{
  vertical-align: middle;
  padding-left: 8px;
  margin-bottom: 5px;
}

.down p{
  color: #eee;
  font-size: 12px;
}
.icon_login{
  display: block;
  width:38px;
  height: 38px;
}
 .icon_login_user{
  background-image: url('../assets/login_images/nn1.png');
