.clickimg {
  border: 1px solid #ccc;
  padding: 2px;
  text-align: center;
  width: 150px;
  height: 150px;
  display: inline-block;
  margin-left: 5px;
  line-height: 150px;
  position: relative;
  cursor: pointer;
}
.clickimg:hover:before {
    display: block;
}
.clickimg:before {
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4);
    speak: none;
    font-style: normal;
    font-weight: 400;
    font-variant: normal;
    text-transform: none;
    content: "\E641";
    left: 0;
    top: 0;
    display: none;
    font-family: element-icons !important;
    cursor: pointer;
    color: #fff;
    font-size: 32px;
}
.clickimg img {
    max-width: 100%;
    max-height: 100%;
    vertical-align: middle;
}
.showPictureWrapper {
  width: auto;
  min-width: 100px;
  max-width: 80%;
  max-height: 100%;
}
.showPictureWrapper .el-message-box__content, .showPictureWrapper .showPictureBox, .showPictureWrapper .el-message-box__message, .showPictureWrapper .el-message-box__message p {
    max-height: 100%;
}
.showPictureWrapper .showPictureBox img {
    max-width: 100%;
    max-height: 100%;
}
.popperHide {
  display: none !important;
}

