
.setUserAuthPop {
  left: auto;
  top: 50px;
  bottom: auto;
  min-width: 320px;
  max-width: 320px;
}
.setUserAuthPop table {
    width: 100%;
}
.setUserAuthPop th, .setUserAuthPop td {
    border: 1px solid #ccc;
    padding: 5px;
    text-align: center;
    font-size: 14px;
}
.setUserAuthPop thead th {
    background: #eee;
}
.setUserAuthPop .popRight-content {
    padding: 20px 20px 0;
}
.setUserAuthPop .el-select .el-input__inner {
    padding-right: 15px;
}



