
.tableHeadItemBtn {
  height: 100%;
  position: relative;
}
.tableHeadItemBtn .el-button {
    border: none;
}
.tableHeadItemBtn .el-button--mini,
  .tableHeadItemBtn .el-button--mini.is-round {
    padding: 5px 9px;
}
.tableHeadItemBtn .tableItemBtnMinus,
  .tableHeadItemBtn .tableItemBtn {
    width: 18px;
    height: 18px;
    background-size: 18px;
    background-repeat: no-repeat;
}
.tableHeadItemBtn .tableAllBtnMinus,
  .tableHeadItemBtn .tableAllBtn {
    width: 18px;
    height: 18px;
    position: absolute;
    z-index: 33;
    top: 10px;
    left: 66px;
    background-size: 18px;
    background-repeat: no-repeat;
}
.tableHeadItemBtn .tableAllBtn,
  .tableHeadItemBtn .tableItemBtn {
    background-image: url("../../../../assets/png/01zengjia-c.png");
}
.tableHeadItemBtn .tableAllBtn:hover,
  .tableHeadItemBtn .tableItemBtn:hover {
    background-image: url("../../../../assets/png/02zengjia.png");
}
.tableHeadItemBtn .tableAllBtnMinus,
  .tableHeadItemBtn .tableItemBtnMinus {
    background-image: url("../../../../assets/png/03shanqu-c.png");
}
.tableHeadItemBtn .tableAllBtnMinus:hover,
  .tableHeadItemBtn .tableItemBtnMinus:hover {
    background-image: url("../../../../assets/png/04shanqu.png");
}
.tableHeadItemBtn .showAllTable {
    width: calc(100% - 100px);
}



