
.screenLockPop .el-dialog {
  min-width: 300px;
  max-width: 400px;
}
.screenLockPop .el-dialog p {
    text-align: center;
}
.screenLockPop .el-dialog .el-dialog__header {
    padding-top: 10px;
    border-bottom: 1px solid #e6e6e6;
}
.screenLockPop .el-dialog .el-dialog__title {
    font-size: 14px;
    font-weight: bold;
}
.screenLockPop .el-dialog .el-dialog__footer {
    border-top: 1px solid #b6dfff;
    padding-bottom: 10px;
}



