
.paymentStap {
  margin-top: 25vh;
}
.paymentStap .time_input {
    margin-left: 42px;
}
.paymentStap .el-dialog {
    max-width: 445px;
    border: 1px solid #cbcbcb;
}
.paymentStap .el-dialog__header {
    background: #e9e9e9;
    border-bottom: 1px solid #cbcbcb;
    padding: 10px 20px 10px;
}
.paymentStap .el-dialog__header span {
      font-size: 15px;
      font-weight: bold;
}
.paymentStap .el-dialog__header .el-dialog__headerbtn {
      position: absolute;
      top: 14px;
      right: 20px;
      padding: 0;
      background: 0 0;
      border: none;
      outline: 0;
      cursor: pointer;
      font-size: 18px;
}
.paymentStap .el-dialog__body {
    min-height: 80px;
}
.paymentStap .el-dialog__footer {
    background: #e9e9e9;
    border-top: 1px solid #cbcbcb;
    padding: 9px 20px 9px;
}
.paymentStap .el-dialog__footer .el-button {
      padding: 5px 8px;
}
.paymentStap .el-dialog__footer .el-button--primary {
      background-color: #0697ce;
}
.paymentStap .el-dialog__footer .el-button--default {
      color: #0697ce;
}
.paymentStap .title {
    font-size: 15px;
    color: #000000;
    text-align: center;
    margin: 0;
}
.paymentStap .title i {
      color: #93c0e9;
      margin-right: 10px;
      font-size: 17px;
}
.paymentStap .title span {
      font-size: 23px;
      color: #f02222;
      font-weight: bold;
}



