
.dashboard-container {
  padding: 30px;
}
.dashboard-text {
  font-size: 30px;
  line-height: 46px;
}
.index-flow {
  width: 1171px;
  height: 654px;
  margin: 53px auto 0;
  position: relative;
  background: url("../../assets/flow/index-bg.png") no-repeat;
  /* background: url("../../assets/flow/index.jpg") no-repeat -3px -4px; */
}
.index-flow .flowitem {
    position: absolute;
    width: 80px;
    height: 90px;
    text-align: center;
    position: absolute;
    transition: all .5s ease;
}
.index-flow .flowitem:hover {
      transform: translate(0, -1px);
}
.index-flow .flowitem i {
      display: block;
      margin: 0 auto;
}
.index-flow .flowitem p {
      display: inline-block;
      white-space: nowrap;
      font-size: 14px;
}
