
@charset "UTF-8";
/** - **/
/** - **/
/** - **/
.sidebar-menu {
  color: rgba(255, 255, 255, 0.8);
}
.sidebar-menu > .menu-item, .sidebar-menu > .sidebar-submenu {
    background: #42485b;
}
.sidebar-menu > .menu-item > a {
    padding-left: 24px;
}
.sidebar-menu .sidebar-submenu {
    display: none;
}
.sidebar-menu a {
    display: block;
}
.sidebar-menu .isOpen > .sidebar-submenu {
    display: block;
}
.sidebar-menu .menu-item {
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.8);
    position: relative;
    transition: height 1s ease;
}
.sidebar-menu .isOpen {
    height: auto;
}
.sidebar-menu .isOpen .dropdownIcon {
      transform-origin: center center;
      transform: rotate(180deg);
}
.sidebar-menu .sidebar-nav-title {
    display: inline-block;
}
.sidebar-menu .sidebar-submenu .submenu-item {
    min-width: 100%;
    padding-left: 50.8px;
    font-size: 12px;
}
.sidebar-menu > .is-active {
    background: #00c1de;
}
.sidebar-menu > .is-active .is-active {
      border: none;
}
.sidebar-menu > .isOpen {
    background: #333744;
}
.sidebar-menu > .isOpen .is-active {
      border: none;
}
.sidebar-menu .is-active > a {
    color: #fff;
}
.sidebar-menu .sidebar_menu_toggle, .sidebar-menu .submenu-item-tab {
    cursor: pointer;
    display: block;
    padding-left: 24px;
}
.sidebar-menu .menu-item:focus, .sidebar-menu .menu-item:hover, .sidebar-menu .sidebar_menu_toggle:hover, .sidebar-menu .sidebar-submenu .submenu-item:hover, .sidebar-menu .submenu-item.is-active {
    background: #00c1de;
}
.sidebar-menu > .isOpen:hover {
    background: #333744;
}
.sidebar-menu > .isOpen:hover .is-active {
      border: none;
}
.sidebar-menu .svg-icon {
    margin-right: 6px;
    font-size: 1.2em;
    color: #fff;
}
.sidebar-menu .dropdownIcon {
    position: absolute;
    top: 12px;
    right: 5px;
    cursor: pointer;
    transition: transform .6s ease;
}

/**  **/
.hideSidebar .sidebar-menu {
  position: relative;
  z-index: 99;
}
.hideSidebar .sidebar-menu .sidebar_menu_toggle {
    text-align: center;
}
.hideSidebar .sidebar-menu .menu-item {
    padding-top: 5px;
    padding-bottom: 25px;
    height: auto;
    font-size: 12px;
    min-height: 40px;
    margin-bottom: 0px;
}
.hideSidebar .sidebar-menu .menu-item > a > .sidebar-nav-title {
      line-height: 20px;
      min-height: auto;
}
.hideSidebar .sidebar-menu .isOpen:hover {
    background: #00c1de;
}
.hideSidebar .sidebar-menu .isOpen .sidebar-submenu {
    display: none;
}
.hideSidebar .sidebar-menu .menu-item:hover .sidebar-submenu {
    background: #42485b;
    display: none;
}
.hideSidebar .sidebar-menu .svg-icon {
    margin-left: -4px;
}
.hideSidebar .sidebar-menu .sidebar-submenu {
    display: none;
}
.hideSidebar .sidebar-menu .el-menu--popup {
    background: #42485b;
}
.hideSidebar .sidebar-menu .el-submenu__icon-arrow {
    display: none;
}
.hideSidebar .sidebar-menu a:hover {
    color: #3e9ff1;
}
.hideSidebar .sidebar-menu .dropdownIcon {
    display: none;
}
.hideSidebar .sidebar-menu .menu-item > a > .sidebar-nav-title, .hideSidebar .sidebar-menu .sidebar_menu_toggle .sidebar-nav-title {
    position: absolute;
    left: 0;
    width: 100%;
    top: 20px;
    text-align: center;
    line-height: 22px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.hideSidebar .sidebar-menu .el-menu-item, .hideSidebar .sidebar-menu .sidebar-nav-title {
    min-height: 44px;
    height: auto;
}
.hideSidebar .sidebar-menu .el-submenu {
    min-width: auto;
}
.hideSidebar .sidebar-submenu {
  width: 130px;
  background: #42485b;
}
.hideSidebar .sidebar-submenu .submenu-item {
    line-height: 40px;
    color: #fff;
    font-size: 12px;
}
.hideSidebar .sidebar-submenu .submenu-item a {
      display: block;
      padding-left: 34px;
}
.hideSidebar .sidebar-submenu .submenu-item.is-active, .hideSidebar .sidebar-submenu .submenu-item:hover {
      background: #00c1de;
}



