
.detailTables {
  margin-top: 75px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: calc(100vh - 240px);
  padding: 0 10px;
  position: relative;
}
.detailTables .detailTables_info {
    border-bottom: 2px dotted #eee;
    background-color: #fff;
}
.detailTables .detailTables_info table {
      width: 100%;
}
.detailTables .detailTables_info table tbody tr {
        font-size: 14px;
}
.detailTables .detailTables_info table tbody tr th {
          color: #666;
          width: 12%;
          height: 36px;
}
.detailTables .detailTables_info table tbody tr td {
          width: 21.3%;
          padding: 0 3px;
}
.detailTables .detailTables_info table tbody tr td .el-input.is-disabled .el-input__inner {
            background-color: #fff;
            color: #3e9ff1;
}
.detailTables .detailTables_info_artery {
    margin-top: 5px;
}
.detailTables .detailTables_info_artery tr th {
      font-size: 12px;
      color: #666;
}
.detailTables .detailTables_info_artery tr td {
      font-size: 13px;
      color: #3e9ff1;
}
.detailTables .detailTables_info_artery tr td,
    .detailTables .detailTables_info_artery tr th {
      height: 35px;
      padding: 10px 5px;
      border: 1px solid #d0d7e5;
      border-collapse: collapse;
      text-align: center;
}
.detailTables .tab_infos {
    padding: 10px 0;
    height: 100%;
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.detailTables .tab_infos .btns_box {
      margin-bottom: 5px;
}
.detailTables .tab_infos .btns_box .el-button {
        margin-right: 0;
}
.detailTables .tab_infos .btns_box .table_setup {
        float: right;
        margin-left: 10px;
}
.detailTables .tab_infos .info_tab {
      width: 100%;
      height: calc(100vh - 430px);
      -ms-flex-positive: 1;
          flex-grow: 1;
}



