
.feeSetupDialog {
  margin-top: 20px !important;
}
.feePopSetup .el-dialog__header, .feePopSetup .el-dialog__footer {
  background: #eee;
  padding: 10px;
}
.feePopSetup .el-dialog__headerbtn {
  top: 10px;
  right: 10px;
}
.feePopSetup .el-dialog__body {
  padding: 35px 10px 20px;
  position: relative;
}
.feePopSetup .el-dialog__title {
  font-size: 14px;
}
.feePopSetup .el-switch__label {
  color: #999;
  font-size: 12px;
}
.feePopSetup .el-switch__label.is-active {
  color: #409EFF;
}
.feePopSetup .setup-info-fee {
  height: 258px;
  overflow: auto;
}
.feePopSetup .list-complete-item {
  clear: both;
  overflow: hidden;
  padding: 3px 0;
  border: 1px solid #fff;
}
.feePopSetup .list-complete-item .el-checkbox {
    float: left;
    width: 186px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.feePopSetup .list-complete-item .switch-box {
    float: right;
    width: 70px;
}
.feePopSetup .searchinput, .feePopSetup .tip-info {
  position: absolute;
  width: 100%;
  left: 0;
}
.feePopSetup .searchinput {
  top: 5px;
  width: 90%;
  left: 5%;
}
.feePopSetup .tip-info {
  font-size: 12px;
  color: #999;
  text-align: center;
  line-height: 20px;
  bottom: 0;
}
.feePopSetup .sortable-ghost {
  border: 1px dashed #f00;
  background: #ffe;
}



