
.pick-maintain .pickpopDepMain {
  top: 29%;
  bottom: auto;
  min-width: 600px;
  max-width: 600px;
}
.pick-maintain .popRight-content {
  padding: 5px 10px 5px 10px;
  box-sizing: border-box;
}
.pick-maintain .popRight-content .el-form--inline .el-form-item {
    margin-right: -8px;
    margin-bottom: 5px;
}
.pick-maintain .popRight-content .el-input {
    width: 90%;
}
.pick-maintain .popRight-content .order_bottom {
    padding-top: 5px;
}
.pick-maintain .popRight-content .order_bottom .el-form-item {
      margin-right: 0;
      margin-bottom: 0;
}
.pick-maintain .popRight-content .order_bottom .el-input {
      width: 125px;
}
.pick-maintain .popRight-content .el-input__inner {
    height: 30px;
    font-size: 14px;
}
.pick-maintain .popRight-content .el-input.is-disabled .el-input__inner {
    background-color: #fff;
    /*border-color: #e4e7ed;*/
    color: #606266;
    /*cursor: not-allowed;*/
}
.pick_center {
  text-align: center;
  padding-top: 80px;
  font-size: 16px;
}
.pick_input {
  padding-top: 60px;
  text-align: center;
  font-size: 14px;
}
.pick_input .el-input {
    width: 40%;
}



