
.sms .sms_top {
  margin: 10px;
  padding: 8px 16px;
  background-color: #ecf8ff;
  border-radius: 4px;
  border-left: 5px solid #50bfff;
}
.sms .sms_top b {
    color: red;
    padding: 0 5px;
}
.sms .sms_content {
  margin: 10px;
}
.sms .sms_content .smsCard {
    border: 1px solid #d0d7e5;
}
.sms .sms_content .smsCard .smsCard_content {
      text-align: center;
      padding-bottom: 20px;
}
.sms .sms_content .smsCard .smsCard_content p {
        font-size: 18px;
        font-weight: 700;
        padding: 20px 0;
}
.sms .sms_content .smsCard .smsCard_content h3 {
        font-size: 18px;
        font-weight: 700;
        margin-bottom: 10px;
}
.sms .sms_content .smsCard .smsCard_content h4 {
        color: #666666;
}
.sms .sms_content .smsCard .smsCard_content .smsCard_footbtn {
        margin: 20px 0 0;
}
.sms .sms_content .el-card .el-card__header {
    text-align: center;
    padding: 10px 10px;
    border-bottom: 1px solid #d0d7e5;
}
.sms .sms_content .el-card .el-card__body {
    padding: 0px;
}



