
.transferTable {
  width: 100%;
  height: calc(100% - 60px);
}
.transferTable .transferTable_header {
    margin-top: 10px;
    width: 100%;
    height: 42px;
    padding: 6px 0;
    text-align: center;
    position: relative;
    border: 2px solid #cdf;
    border-bottom: none;
}
.transferTable .transferTable_header .transferTable_searchs {
      position: absolute;
      top: 0px;
      left: 0px;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-direction: row;
          flex-direction: row;
}
.transferTable .transferTable_header .transferTable_header_btn_direction {
      text-align: center;
      margin-left: -7px;
}
.transferTable .transferTable_header .transferTable_fresh {
      padding-top: 6px;
      padding-left: 10px;
}
.transferTable .transferTable_header .transferTable_header_btn_box {
      position: absolute;
      right: 10px;
      top: 6px;
}
.transferTable .transferTable_content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    height: 100%;
}
.transferTable .transferTable_content .paddingRight {
      padding-right: 10px;
}
.transferTable .transferTable_content .transferTable_content_table {
      width: 50%;
      height: 100%;
      transition: 0.5s;
      position: relative;
}
.transferTable .transferTable_content .transferTable_content_table .el-table table {
        width: 100%;
}
.transferTable .transferTable_content .transferTable_content_table .el-table table th,
        .transferTable .transferTable_content .transferTable_content_table .el-table table td {
          text-align: center;
}
.transferTable .transferTable_content .transferTable_content_table .el-table td,
      .transferTable .transferTable_content .transferTable_content_table .el-table th {
        padding: 5px 0;
}
.transferTable .transferTable_content .showTableLeft {
      width: calc(100% - 90px);
      transition: 0.5s;
}
.transferTable .transferTable_content .shortTableLeft {
      width: 112px;
      transition: 0.5s;
}
.transferTable .transferTable_content .showTableRight {
      width: calc(100% - 112px);
      transition: 0.5s;
}



