
@charset "UTF-8";
.setpinfo_box {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  min-width: 1060px;
  /* ele */
  /*  */
}
.setpinfo_box .el-form--inline .el-form-item {
    margin-bottom: 0;
}
.setpinfo_box .el-step.is-vertical .el-step__head {
    width: 18px;
    position: relative;
    top: 5px;
}
.setpinfo_box .el-step.is-vertical .el-step__line {
    left: 8px;
}
.setpinfo_box .el-step__icon.is-text {
    border-color: transparent;
    border: 0;
    width: 18px;
    height: 18px;
}
.setpinfo_box .el-steps--vertical {
    height: auto;
}
.setpinfo_box .el-step__main {
    padding-bottom: 10px;
}
.setpinfo_box .el-step__description {
    padding-right: 0;
    font-size: 14px;
    color: #333;
}
.setpinfo_box .textcenter {
    text-align: center;
}
.setpinfo_box .typebox {
    background: url("../../../../assets/png/track-bg.png") no-repeat;
    width: 118px;
    height: 24px;
    padding-left: 16px;
    text-align: center;
    color: #333;
    display: inline-block;
    line-height: 24px;
}
.setpinfo_box .location {
    background: url("../../../../assets/png/unactive-icon.png") no-repeat;
    width: 18px;
    height: 18px;
    display: inline-block;
}
.setpinfo_box .modifybtn, .setpinfo_box .deletebtn {
    display: inline-block;
    display: none;
    vertical-align: middle;
    width: 20px;
    height: 20px;
    cursor: pointer;
    margin-right: 3px;
    background: url("../../../../assets/png/delete-icon.png") no-repeat;
}
.setpinfo_box .trackactive .modifybtn, .setpinfo_box .trackactive .deletebtn {
    display: inline-block;
}
.setpinfo_box .trackactive .typebox, .setpinfo_box .firstactive .typebox {
    background: url("../../../../assets/png/track-active.png") no-repeat;
    color: #fff;
}
.setpinfo_box .trackactive .location, .setpinfo_box .firstactive .location {
    background: url("../../../../assets/png/active-point.png") no-repeat;
}
.setpinfo_box .trackactive .el-step__line, .setpinfo_box .firstactive .el-step__line {
    background-color: #3e9ff1;
}
.setpinfo_box .modifybtn {
    margin-left: 2px;
    background: url("../../../../assets/png/edit-icon.png") no-repeat;
    margin-right: 8px;
}
.setpinfo_box .steptitle {
    font-size: 14px;
    color: #333;
    height: 34px;
}
.setpinfo_box .stepItem_title {
    color: #666;
    padding-left: 28px;
    height: 34px;
}
.setpinfo_box .stepItem_title .el-col {
      text-align: left;
}
.setpinfo_box .stepItem_title .tracktype {
      text-align: left;
      text-indent: 20px;
}
.setpinfo_box .hyq-control-info {
    padding-right: 10px;
}
.setpinfo_box .stepItem {
    /* 0 */
}
.setpinfo_box .stepItem .el-col > p {
      min-width: 1px;
      min-height: 1px;
}
.setpinfo_box .stepinfo {
    -ms-flex: 1;
        flex: 1;
    overflow: auto;
}
.setpinfo_box .stepinfo-footer {
    border-top: 1px solid #d8d8d8;
    height: 70px;
    padding-top: 30px;
}



