/** - **/
/** - **/
.topNav-search {
  position: absolute;
  top: 0;
  right: 320px;
  height: 50px;
  line-height: 50px;
}
.topNav-search .topNavSearch-trigger {
    width: 50px;
    text-align: center;
    background: rgba(0, 0, 0, 0.05);
    cursor: pointer;
}
.topNav-search .topNavSearch-trigger:hover {
      background: rgba(0, 0, 0, 0.1);
}
.topNav-search .topNavSearch-trigger .el-icon-search {
      color: #fff;
}
.topNav-search .el-input__inner {
    width: 160px;
    height: 26px;
    border-radius: 12px;
    border: solid 1px rgba(255, 255, 255, 0.69);
    background: #42485B;
    color: rgba(255, 255, 255, 0.9);
    font-size: 12px;
}
.topNav-search .el-icon-circle-close {
    cursor: pointer;
}
.topNav-search .el-icon-circle-close:hover {
      color: #333;
}
.longSearchBox .el-input__inner {
  width: 400px;
}
.my-topNav-search .el-autocomplete-suggestion__wrap {
  padding: 0;
}
.my-topNav-search .el-autocomplete-suggestion__wrap li {
    padding-left: 0;
    padding-right: 0;
}
.my-topNav-search .name {
  display: inline-block;
  width: 80px;
  background: rgba(0, 0, 0, 0.1);
  text-align: right;
  padding-right: 3px;
  font-size: 14px;
}
@media screen and (max-width: 1300px) {
.my-topNav-search-input {
    display: none;
    position: absolute;
    top: 44px;
    right: 0;
    width: 200px;
    line-height: 40px;
}
.my-topNav-search-input .el-icon-search {
      display: none;
}
.my-topNav-search-input .el-input--prefix .el-input__inner {
      padding-left: 10px;
      width: 100%;
      padding-right: 10px;
      border: 1px solid #1C6094;
}
.my-topNav-search-input .svg-icon {
      display: none;
}
.longSearchBox .el-input__inner {
    width: 100%;
}
.showMiniSearchBox .my-topNav-search-input {
    display: block;
}
}
@media screen and (min-width: 1300px) {
.topNavSearch-trigger {
    display: none;
}
}



