
@charset "UTF-8";
.tab-content.chartSender {
  width: 100%;
  height: calc(100%);
}
.tab-content.chartSender h2 {
    text-align: center;
    margin: 0px 0px 20px 0;
    color: #409EFF;
}
.tab-content.chartSender .chartIframe {
    width: 100%;
    height: 100%;
    box-shadow: 1px 1px 10px #bbb;
    overflow: hidden;
    scrolling: no;
}
.hidetrigger {
  position: absolute;
  width: calc(100% - 20px);
  height: calc(100% - 100px);
}
.info_tab_report {
  height: calc( 100%);
  overflow: auto;
  border: 1px solid #d0d7e5;
  box-shadow: 1px 1px 20px #ddd;
  /**/
}
.info_tab_report table {
    width: 100%;
    min-width: 1000px;
}
.info_tab_report table tbody tr {
      background-color: #FFF;
      transition: 0.5s;
}
.info_tab_report table tbody tr:hover {
      background-color: #ccc;
      transition: 0.3s;
}
.info_tab_report table tbody tr td:hover {
      background-color: #cdcdcd;
      transition: 0.3s;
}
.info_tab_report table tbody,
    .info_tab_report table tfoot {
      color: #222;
      line-height: 23px;
      font-size: 13px;
}
.info_tab_report table tbody td,
      .info_tab_report table tfoot td {
        font-size: 13px;
}



