
.rechargePop {
  height: 404px;
  top: 50%;
  margin-top: -202px !important;
}
.rechargePop .el-dialog__header {
    border-bottom: 1px solid #ccc;
}
.rechargePop .recharge-panel .recharge-fee {
    padding-left: 4em;
    margin-bottom: 10px;
}
.rechargePop .recharge-panel .recharge-label {
    float: left;
    width: 4em;
    margin-left: -4em;
    line-height: 26px;
}
.rechargePop .recharge-panel .recharge-label span {
      text-align: center;
      font-size: 12px;
      color: #999;
      line-height: 20px;
      display: block;
}
.rechargePop .recharge-panel .chargefee-list {
    text-align: justify;
}
.rechargePop .recharge-panel .nativeinput {
    height: 30px;
    width: 328px;
    border: 1px solid #c9c9c9;
    border-left: none;
}
.rechargePop .recharge-panel .otherrecharge-fee {
    position: relative;
}
.rechargePop .recharge-panel .otherrecharge-fee .charefee-opt {
      float: left;
      vertical-align: middle;
}
.rechargePop .recharge-panel .otherfee-error {
    position: absolute;
    bottom: -12.5px;
    left: 86px;
    font-size: 12px;
    color: #ef0000;
}
.rechargePop .recharge-panel .charefee-opt {
    position: relative;
    display: inline-block;
    width: 70px;
    height: 30px;
    border: 1px solid #C9C9C9;
    text-align: center;
    line-height: 30px;
    margin-left: 12px;
    margin-bottom: 6px;
    overflow: hidden;
    cursor: pointer;
}
.rechargePop .recharge-panel .charefee-opt i {
      display: none;
      position: absolute;
      top: -2px;
      right: -2px;
      font-size: 10px;
}
.rechargePop .recharge-panel .charefee-opt:hover {
      border-color: #0099FF;
}
.rechargePop .recharge-panel .charefee-opt.select-opt {
      border-color: #0099FF;
      color: #0099FF;
      cursor: default;
}
.rechargePop .recharge-panel .charefee-opt.select-opt i {
        display: block;
}
.rechargePop .recharge-panel .payway-title {
    margin-bottom: 10px;
}
.rechargePop .recharge-panel .recharge-payway {
    padding-bottom: 25px;
    border-bottom: 1px solid #ccc;
    margin-bottom: 35px;
}
.rechargePop .recharge-panel .el-button {
    width: 100%;
    display: block;
    font-size: 24px;
    letter-spacing: 20px;
}
.rechargePop .payway-list span {
    display: inline-block;
    width: 170px;
    height: 56px;
    border: 1px solid #ccc;
    cursor: pointer;
}
.rechargePop .payway-list span.active {
      border-color: #0099FF;
      cursor: default;
}
.rechargePop .hyq-wx {
    float: left;
    background: url(../../../../assets/weipay.png);
}
.rechargePop .hyq-ali {
    float: right;
    background: url(../../../../assets/alipay.png);
}
.rechargePop .twocode-panel {
    text-align: center;
    padding-top: 10px;
}
.rechargePop .twocode-panel .hyq-tc-title {
      margin-bottom: 10px;
}
.rechargePop .twocode-panel .hyq-tc-box {
      width: 220px;
      height: 220px;
      border: 1px solid #56AC65;
      margin: 10px auto;
}
.rechargePop .twocode-panel img {
      width: 100%;
      height: 100%;
}
.rechargePop .twocode-panel .hyq-pay-success {
      color: darkgreen;
      font-weight: bold;
}
.rechargePop .twocode-panel .hyq-pay-fail {
      color: darkred;
      font-weight: bold;
}



