#app .sidebar-menu {
  height: 100%;
  overflow-y: auto;
  overflow-x: visible;
  width: 100%;
}
#app .sidebar-menu::-webkit-scrollbar-track-piece {
    background: transparent;
}
#app .sidebar-menu::-webkit-scrollbar {
    width: 0;
}
#app .sidebar-menu::-webkit-scrollbar-thumb {
    background: transparent;
    border-radius: 0;
}
#app .sidebar-menu .router-link-exact-active .el-menu-item {
    color: #20a0ff;
}
.sidebar-container {
  min-height: 100%;
  padding-top: 70px;
  background: #42485B;
  border-radius: 0;
}
.sidebar-container .el-menu {
    border-right: 0;
    background: #333744;
}
.sidebar-container .sidebar-userinfo {
    margin-top: -40px;
    margin-bottom: 15px;
}
.sidebar-container .sidebar-userinfo img {
      float: left;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      margin-right: 10px;
      margin-left: 5px;
}
.sidebar-container .sidebar-userinfo span {
      display: inline-block;
      width: 100px;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      font-size: 14px;
      color: #eee;
      line-height: 20px;
}
.sidebar-container .hamburger-container {
    position: absolute;
    top: 40px;
    right: 10px;
}
.sidebar-container .subNavWrapper {
    display: none;
    position: absolute;
    left: 100%;
    max-height: 100%;
    overflow: auto;
}
.nologin {
  position: absolute;
  width: 168px;
  bottom: 0;
  left: 0;
  text-align: center;
}
.nologin .el-button2 {
    margin-bottom: 10px;
    width: 150px;
}
.nologin .el-button {
    width: 100%;
    height: 50px;
    background-color: #002038;
    border: 4px solid #000;
    border-top-color: #05375d;
    border-right-color: #05375d;
    border-left-color: #051527;
    border-bottom-color: #051527;
    border-radius: 0;
    font-size: 16px;
    color: rgba(255, 255, 255, 0.65);
}
.nologin .el-button:hover {
      opacity: 1;
}



