#app .page-top-nav .el-menu--horizontal {
  border-bottom: none;
}
#app .page-top-nav .el-menu {
  background: #373d41;
}
#app .page-top-nav .el-menu-item {
  height: 50px;
  line-height: 50px;
  color: #fff;
  font-size: 16px;
}
#app .page-top-nav .el-menu-item.is-active {
    background: #2a2f32;
    border-bottom: none;
}
#app .page-top-nav .el-menu-item .svg-icon {
    margin-right: 0;
    vertical-align: -4px;
    font-size: 1.2em;
}
#app .page-top-nav .el-menu-item:hover {
    background: #2a2f32;
}
#app .page-top-nav .el-submenu > .el-menu {
  top: 52px;
}



