
@charset "UTF-8";
.html5index2 {
  min-width: 1100px;
  height: 100%;
  min-height: 666px;
  background: #ebebeb;
}
.main_html5_content {
  padding: 15px;
  overflow-y: hidden;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}
.main_html5_content .head_title {
    height: 31px;
    line-height: 31px;
    overflow: hidden;
    border-radius: 2px;
    background: #fff;
    box-sizing: border-box;
    position: relative;
}
.main_html5_content .head_title h4 {
      font-size: 14px;
      display: inline-block;
}
.main_html5_content .head_title .getorglist {
      border-left: 1px solid #eee;
      position: absolute;
      right: 10px;
      top: 0;
      z-index: 2;
      font-size: 14px;
      line-height: 31px;
}
.main_html5_content .head_title .getorglist .el-input--suffix .el-input__inner {
        border: none;
        line-height: 30px;
        height: 30px;
        width: 170px;
}
.main_html5_content .head_title .getorglist .el-input__icon {
        line-height: 30px;
}
.main_html5_content .head_title .getorglist .el-select__tags-text {
        color: #3E9FF1;
}
.main_html5_content .head_title .getorglist .el-select .el-tag {
        background: none;
}
.main_html5_content .head_title ul {
      position: relative;
      width: 100%;
      padding-right: 180px;
      height: 100%;
      text-align: right;
}
.main_html5_content .head_title ul li {
        display: inline-block;
        min-width: 45px;
        text-align: center;
        white-space: nowrap;
        font-size: 14px;
        color: #333;
        /*  */
        cursor: pointer;
        box-sizing: border-box;
}
.main_html5_content .head_title ul li .el-range-editor.el-input__inner {
          height: 4%;
          line-height: 30px;
          border: none;
}
.main_html5_content .head_title ul li .el-date-editor .el-range__close-icon {
          position: absolute;
          right: 10%;
          top: 0%;
}
.main_html5_content .head_title ul li .el-date-editor .el-range__icon {
          display: none;
}
.main_html5_content .head_title ul li .el-date-editor .el-range-separator {
          line-height: 25px;
}
.main_html5_content .head_title ul li .el-range-editor .el-range-input {
          margin-left: 10px !important;
}
.main_html5_content .head_title ul li:hover, .main_html5_content .head_title ul li.active {
        color: #3e9ff1;
        transform: scale(1.4);
}
.main_html5_content .head_title ul li:nth-child(6):hover {
        transform: none;
}
.main_html5_content .databox {
    position: relative;
    height: 31%;
    color: #fff;
    padding-right: 20px;
    text-align: right;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    transition: all .2s ease;
}
.main_html5_content .databox:hover {
      box-shadow: 0 3px 10px rgba(0, 0, 0, 0.3);
}
.main_html5_content .databox .dataico {
      position: absolute;
      left: 20px;
      top: 50%;
      margin-top: -35px;
      font-size: 70px;
      opacity: 0.2;
}
.main_html5_content .databox .databox-value {
      font-size: 48px;
      margin-bottom: 10px;
      display: block;
}
.main_html5_content .databox .databox-label {
      font-size: 16px;
      color: #fff;
      color: rgba(255, 255, 255, 0.6);
}
.main_html5_content .datablue {
    background: #409EFF;
}
.main_html5_content .datared {
    background: #F56C6C;
}
.main_html5_content .datagreen {
    background: #67C23A;
}
.main_html5_content .databox-line {
    height: 3.5%;
}
.main_html5_content .main_forthUl {
    overflow: hidden;
    box-sizing: border-box;
    height: 215px;
}
.main_html5_content .main_forthUl ul {
      box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.1);
      background: #fff;
      height: 185px;
      margin: 15px 0 15px 0;
      box-sizing: border-box;
      position: relative;
      transition: color 4s ease;
      border-radius: 5px;
}
.main_html5_content .main_forthUl ul .box_gg, .main_html5_content .main_forthUl ul .box_gg1 {
        position: absolute;
        width: 8%;
        height: 16%;
        top: 0.1%;
        left: 92.2%;
        display: none;
        background-repeat: no-repeat;
        background-position: right;
        box-sizing: border-box;
        z-index: 10000;
}
.main_html5_content .main_forthUl ul .box_gg img, .main_html5_content .main_forthUl ul .box_gg1 img {
          background-size: cover;
          width: 100%;
          height: 100%;
}
.main_html5_content .main_forthUl ul.zhichulie li span {
        min-width: 4em;
        text-align: center;
        display: inline-block;
}
.main_html5_content .main_forthUl ul li {
        height: 30px;
        line-height: 30px;
        font-size: 14px;
        padding: 0 20px;
        box-sizing: border-box;
        border-radius: 5px 5px 0 0;
}
.main_html5_content .main_forthUl ul li .marginem {
          display: inline-block;
          width: 2em;
}
.main_html5_content .main_forthUl ul li .marginleft {
          margin-left: 2em;
}
.main_html5_content .main_forthUl ul li span {
          color: red;
          padding: 0 5px;
}
.main_html5_content .main_forthUl ul li em {
          float: right;
}
.main_html5_content .main_forthUl ul li em i {
            margin-left: 5px;
}
.main_html5_content .main_forthUl ul li:nth-child(1) {
        border-bottom: 1px solid #ddd;
        font-weight: bold;
        font-size: 16px;
}
.main_html5_content .main_forthUl ul li.profit-num {
        text-align: center;
        line-height: 1.5;
}
.main_html5_content .main_forthUl ul li.profit-num .xdata span {
          font-size: 36px;
          max-width: 7em;
          white-space: nowrap;
          display: inline-block;
          line-height: 146px;
}
.main_html5_content .main_forthUl ul li.profit-num .xline {
          height: 60px;
          width: 1px;
          background: #999;
          display: inline-block;
          margin: 0 5px;
          transform: rotate(15deg) translate(7px, 15px);
          display: none;
}
.main_html5_content .main_forthUl ul li.profit-num .xtip {
          position: absolute;
          bottom: 10px;
          right: 10px;
          color: #ef0000;
}
.main_html5_content .main_forthUl ul li.profit-num i {
          font-style: normal;
}
.main_html5_content .main_forthUl ul:nth-child(4) {
      margin: 15px 0 15px 0;
}
.main_html5_content .main_forthUl ul:hover {
    border: 1px solid #3e9ff1;
    cursor: pointer;
    box-sizing: border-box;
    transition: all 0.2s ease-out;
    box-shadow: 0px 35px 77px -17px rgba(0, 0, 0, 0.4);
    overflow: hidden;
    color: #3e9ff1;
}
.main_html5_content .main_forthUl ul:hover .box_gg {
    display: none;
}
.main_html5_content .main_forthUl ul:active .box_gg1 {
    display: none;
}
.main_html5_content .main_left {
    padding: 0 0 !important;
    -ms-flex: 1;
        flex: 1;
    overflow: hidden;
}
.main_html5_content .main_left .el-col {
      height: 100%;
}
.main_html5_content .main_left .ul_left {
      background: #fff;
      height: 100%;
      box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
      border: 1px solid #ddd;
}
.main_html5_content .main_left .ul_right {
      height: 100%;
}
.main_html5_content .main_left .ul_right li {
        width: 100%;
        height: 50%;
        padding-top: 7.5px;
}
.main_html5_content .main_left .ul_right li .inner_div {
          height: 100%;
          background: #fff;
          border-radius: 5px;
          padding-top: 30px;
          box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.1);
          border: 1px solid #ddd;
}
.main_html5_content .main_left .ul_right li .inner_div > div {
            height: 100%;
}
.main_html5_content .main_left .ul_right li canvas {
          height: 100%;
}
.main_html5_content .main_left .ul_right li p {
          margin-top: -30px;
          font-size: 14px;
          padding: 7px;
          border-radius: 5px 5px 0 0;
}
.main_html5_content .main_left .ul_right li:nth-child(1) {
        padding-bottom: 7.5px;
        padding-top: 0;
}
.main_html5_content .main_left .ul_right .inner_div:hover {
        cursor: pointer;
        box-sizing: border-box;
        transition: all 0.2s ease-out;
        box-shadow: 0px 35px 77px -17px rgba(0, 0, 0, 0.1);
        overflow: hidden;
        color: #3e9ff1;
        transform: translateY(-3px);
        transition-delay: 0.2s;
}
.main_html5_content .main_left .ul_right #main {
        height: 100%;
}



