
.staff_manage {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: relative;
}
.staff_manage .staff_info {
    padding: 10px 10px 40px;
    height: 100%;
    -ms-flex-positive: 1;
        flex-grow: 1;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
}
.staff_manage .staff_info .btns_box {
      margin-bottom: 10px;
}
.staff_manage .staff_info .btns_box .table_setup {
        float: right;
        margin-right: 0;
}
.staff_manage .staff_info .info_news {
      width: 100%;
      height: calc(100% - 90px);
      -ms-flex-positive: 1;
          flex-grow: 1;
}
.staff_manage .staff_info .info_news .el-table table th,
      .staff_manage .staff_info .info_news .el-table table td {
        text-align: center;
}
.staff_manage .staff_info .info_news .el-table .unauth {
        color: #f00;
}
.staff_manage .info_news_footer {
    padding-left: 20px;
    background: #eee;
    height: 40px;
    line-height: 40px;
    box-shadow: 0 -2px 2px rgba(0, 0, 0, 0.1);
    position: relative;
    z-index: 10;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
}
.staff_manage .show_pager {
    float: right;
    line-height: 40px;
    height: 40px;
    overflow: hidden;
}



